import { TripGraph } from './trip-graph';
import { TripEdge, TripEdgeProperties } from './trip-edge';
import { TripNode, TripNodeProperties } from './trip-node';
import { BoundingBox } from './bounding-box';
import { Size2D } from './size2d';
import { TripSaved } from './trip-saved';
import { Point, IPoint } from './point';
import { T, Location, CanvasTilesRenderer, CanvasTilesRendererParams, PinchZoom } from './tiles';

declare const d3: any;
declare const Bezier: any;

export interface TripGraphLayerParams {
  renderer: CanvasTilesRenderer;
  graph: TripGraph;
  defaultRadius?: number;
  defaultFontSize?: number;
  defaultEdgeProp?: TripEdgeProperties;
  hideLabels?: boolean;
  hideNodePoints?: boolean;
};

export class TripGraphLayer {
  public graph: TripGraph;
  public defaultTextProp: TripNodeProperties;
  public defaultEdgeProp: TripEdgeProperties;

  public renderer: CanvasTilesRenderer;
  public defaultRadius: number;
  private icons: any;

  private iconsToLoad: number;
  private failedIcons: number;
  public hideLabels: boolean;
  public hideNodePoints: boolean;
  public forceLeaderLines: boolean;

  public markedPlaces: IPoint[];

  private missingData: number;

  constructor(private params: TripGraphLayerParams) {
    this.markedPlaces = [];
    this.renderer = params.renderer;
    this.defaultRadius = params.defaultRadius || 6;
    this.icons = {};
    this.defaultTextProp = {
      fontSize: params.defaultFontSize || 20,
      stroke: 'rgba(255,255,255,.8)',
      fill: '#000000'
    };
    this.defaultEdgeProp =
      params.defaultEdgeProp || { lineWidth: 1, lineColor: '#000000' };
    this.hideLabels = !!params.hideLabels;
    this.hideNodePoints = !!params.hideNodePoints;

    if (!this.renderer) {
      throw(new Error('TripGraphLayer: no renderer !'));
    }
    this.graph = params.graph || new TripGraph(undefined, this.defaultEdgeProp);
    this.missingData = 1;
  }

  isReady() : boolean {
    return this.missingData == 0;
  }

  draw(canvas: HTMLCanvasElement, pinchZoom: any, bboxTopLeft: any, bboxBottomRight: any) {
    const graph = this.graph;
    const context = canvas.getContext('2d');
    const pixelRatio = this.renderer.pixelRatio;

    this.markedPlaces = [];
    this.missingData = 0;

    for (let e of graph.edges) {
      this.drawEdge(context, pinchZoom, e, pixelRatio, 'curve');
    }
    for (let e of graph.edges) {
      this.drawEdge(context, pinchZoom, e, pixelRatio, 'mark');
    }

    context.setLineDash([]);

    for (let i in graph.nodes) {
      const node = graph.nodes[i];
      node.convertToPixelOffset(pinchZoom, pixelRatio);
      node.viewerPos = pinchZoom.viewerPosFromWorldPos(node.coord);
    }

    if (!this.hideNodePoints) {
      for (let i in graph.nodes) {
        const node = graph.nodes[i];
        this.drawNodePoint(context, pinchZoom, node);
      }
    }

    for (let i in graph.nodes) {
      if (this.forceLeaderLines ||
          !this.hideLabels || i.match(/-(takeoff)|(landing)$/)) {
        const node = graph.nodes[i];
        this.drawLeaderLine(context, pinchZoom, node);
      }
    }
    for (let i in graph.nodes) {
      if (!this.hideLabels || i.match(/-(takeoff)|(landing)$/)) {
        const node = graph.nodes[i];
        this.drawNodeLabel(context, pinchZoom, node);
      }
    }

    this.markedPlaces = this.markedPlaces.map(
        (x) => pinchZoom.worldPosFromViewerPos(x));

    this.fillClicMap(canvas, pinchZoom);
  }

  drawEdge(context: CanvasRenderingContext2D, pinchZoom: any,
           edge: TripEdge, pixelRatio: number, restrict: string) {
    this.setEdgeStrokeStyle(context, edge);

    if (this.edgeProperty(edge, 'geom') == 'route') {
      this.drawRoad(context, pinchZoom, edge, pixelRatio, restrict);
    } else {
      this.drawEdgeBezier(context, pinchZoom, edge, pixelRatio, restrict);
    }
  }

  drawRoad(context, pinchZoom, edge, pixelRatio, restrict) {
    const mean = this.edgeProperty(edge, 'travelMean');
    const route = this.graph.getEdgeRoute(edge);
    if (restrict == 'curve') {
      if (route) {
        const road = edge.properties.route.geom;
        context.beginPath();
        const first = pinchZoom.viewerPosFromWorldPos(road[0]);
        context.moveTo(first.x, first.y);
        for (let i = 1; i < road.length; ++i) {
          const p = pinchZoom.viewerPosFromWorldPos(road[i]);
          context.lineTo(p.x, p.y);
        }
        context.stroke();
      } else {
        // no road yet, let's try to get one.
        this.missingData += 1;
        if (!edge.properties) {
          edge.properties = { };
        }
        this.graph.getEdgeDirections(edge, this.edgeProperty(edge, 'travelMean'))
          .then((data) => { edge.properties.route = data; this.renderer.refresh(); })
          .catch((err) => {
            // We can't get a curve!
            console.log(err);
            // Let's replace it by a straight line.
            edge.properties.geom = 'straight';
            this.renderer.refresh();
          });
      }
    } else if (restrict == 'mark') {
      if (route && route.midpoint) {
        const first = new Point(pinchZoom.viewerPosFromWorldPos(route.geom[0]));
        const last = new Point(pinchZoom.viewerPosFromWorldPos(route.geom[route.geom.length - 1]));
        if (Point.dist(last, first) > 80 * pixelRatio) {
          const mid = route.midpoint;
          // Search for the intersection between the route starting at 'mid'
          // and a circle of radius 16 * pixelRatio (see drawEdgeMarkIcon).
          const circleRadius = 16 * pixelRatio;
          const midInView = new Point(pinchZoom.viewerPosFromWorldPos(route.geom[mid]));
          let tangent;
          for (let i = mid; i < route.geom.length; ++i) {
            const currentPoint = new Point(pinchZoom.viewerPosFromWorldPos(route.geom[i]));
            const dist = Point.dist(midInView, currentPoint);
            if (dist > circleRadius) {
              tangent = Point.minus(currentPoint, midInView);
              break;
            }
          }
          if (!tangent) {
            return;
          }

          const n = new Point(tangent.y, -tangent.x);
          this.drawEdgeMark(
              // we want to place the icon center right on the curve, which is
              // not the default for splines.
              Point.plus(midInView, Point.times(8 * pixelRatio / tangent.norm(), tangent)),
              n, tangent, context, pixelRatio, edge, restrict);
        }
      }
    }
  }

  drawEdgeBezier(context: CanvasRenderingContext2D, pinchZoom: any,
                 edge: TripEdge, pixelRatio: number, restrict: string) {
    const bezierCurves = this.graph.bezier(edge);
    for (let bezier of bezierCurves ) {
      this.drawBezier(context, pinchZoom, bezier, pixelRatio, edge, restrict);
    }
    if (restrict != 'mark') {
      if (bezierCurves.length > 1 && edge.drawMiddlePoint) {
        for (let i in bezierCurves) {
          const p = pinchZoom.viewerPosFromWorldPos(bezierCurves[i].points[3]);
          const radius = 5 * pixelRatio;
          context.beginPath();
          context.arc(p.x, p.y, radius, 0, 2 * Math.PI, false);
          context.fillStyle = '#000000';
          context.fill();
        }
      }
    }
  }

  drawBezier(context: CanvasRenderingContext2D, pinchZoom: any,
                    curve: any, pixelRatio: number,
                    edge: TripEdge, restrict?: string) {
    context.save();

    const ptArray = new Array(8);
    const points = curve.points;
    for (let j = 0; j < 4; ++j) {
      const p = pinchZoom.viewerPosFromWorldPos(points[j]);
      ptArray[j * 2] = p.x;
      ptArray[j * 2 + 1] = p.y;
    }

    const bezier = new Bezier(ptArray);
    const l = bezier.length();
    const numArrows = Math.min(2, Math.floor(l / (pixelRatio * 40)));

    const viewcurve = bezier.points;

    if (restrict != 'mark') {
      context.beginPath();
      context.moveTo(viewcurve[0].x, viewcurve[0].y);
      context.bezierCurveTo(
          viewcurve[1].x, viewcurve[1].y,
          viewcurve[2].x, viewcurve[2].y,
          viewcurve[3].x, viewcurve[3].y);

      context.stroke();
    }

    for (let i = 1; i < numArrows; ++i) {
      const t = i / numArrows;
      let p = new Point(bezier.get(t));
      const n = new Point(bezier.normal(t));
      const tangent = new Point(bezier.derivative(t));

      this.drawEdgeMark(p, n, tangent, context, pixelRatio, edge, restrict);
    }
    context.restore();
  }

  drawEdgeMark(p: Point, n: Point, tangent: Point,
               context: CanvasRenderingContext2D, pixelRatio: number,
               edge: TripEdge, restrict: string) {
    const mean = this.edgeProperty(edge, 'travelMean');
    switch(mean) {
      case 'Bike':
      case 'Boat':
      case 'Bus':
      case 'Car':
      case 'Helicopter':
      case 'Plane':
      case 'Train':
      case 'Walk':
        if (restrict != 'curve') {
          this.drawEdgeMarkIcon(p, n, tangent, context, pixelRatio, edge);
        }
        break;
      case 'Unspecified':
      default:
        if (restrict != 'mark') {
          TripGraphLayer.drawEdgeMarkTriangle(p, n, tangent, context, pixelRatio);
        }
    }
  }

  static drawEdgeMarkTriangle(p: Point, n: Point, tangent: Point,
                      context: CanvasRenderingContext2D, pixelRatio: number) {
    const size = 10 * pixelRatio;
    tangent.mul(size / tangent.norm());
    p = Point.plus(p, Point.times(.5, tangent));
    const base = Point.minus(p, tangent);

    context.beginPath();
    context.moveTo(base.x + (size / 2) * n.x, base.y + (size / 2) * n.y);
    context.lineTo(p.x, p.y);
    context.lineTo(base.x - (size / 2) * n.x, base.y - (size / 2) * n.y);
    context.closePath();
    context.fill();
  }

  drawEdgeMarkIcon(p: Point, n: Point, tangent: Point,
                   context: CanvasRenderingContext2D, pixelRatio: number,
                   edge: TripEdge) {
    context.fillStyle = '#fff';
    context.setLineDash([]);
    context.lineWidth = 1 * pixelRatio;

    const radius = 10 * pixelRatio;
    const arrowLength = 6 * pixelRatio;

    tangent = tangent.mul(1 / tangent.norm());
    const head = Point.plus(p, Point.times((radius + arrowLength) / 2, tangent));
    const center = Point.minus(p, Point.times((radius + arrowLength) / 2, tangent));
    const circleBase = Point.plus(center, Point.times(radius, tangent));
    const delta = (new Point(-tangent.y, tangent.x)).mul(4 * pixelRatio);
    const circleStart = Point.plus(circleBase, delta);
    const circleEnd = Point.minus(circleBase, delta);

    const p1 = Point.minus(circleStart, center);
    const angle1 = Math.atan2(p1.y, p1.x);
    const p2 = Point.minus(circleEnd, center);
    const angle2 = Math.atan2(p2.y, p2.x);

    context.beginPath();
    context.arc(center.x, center.y, radius, angle1, angle2);
    context.lineTo(head.x, head.y);
    context.closePath();
    context.fill();
    context.stroke();

    context.fillStyle = '#000';
    context.textAlign = 'center';
    context.textBaseline="middle"; 
    context.font = Math.round(pixelRatio * 10) + "px FontAwesome";

    const icons = {
      'Bike': "\uf206",
      'Boat': "\uf21a",
      'Bus': "\uf207",
      'Car': "\uf1b9",
      'Helicopter': 'H',
      'Plane': "\uf072",
      'Train': "\uf238",
      'Walk': "\uf183"
    }

    const mean = this.edgeProperty(edge, 'travelMean');
    if (mean == 'Plane') {
      context.save();
      context.translate(center.x, center.y);
      context.rotate(Math.PI / 4 + Math.atan2(tangent.y, tangent.x));
      context.translate(-center.x, -center.y);
    }

    context.fillText(icons[mean], center.x, center.y);
    this.markedPlaces.push(center);

    if (mean == 'Plane') {
      context.restore();
    }
  }

  edgeProperty(edge: TripEdge, prop: string) {
    return edge.properties[prop] || this.defaultEdgeProp[prop];
  }

  setEdgeStrokeStyle(context: CanvasRenderingContext2D, edge: TripEdge) {
    let style = this.edgeProperty(edge, 'lineColor');
    context.strokeStyle = style;
    context.fillStyle = style;
    context.lineWidth = this.renderer.pixelRatio * (
        this.edgeProperty(edge, 'lineWidth'));
    this.setDashStyle(context, this.edgeProperty(edge, 'dashed'));
  };

  nodeRadius(node: TripNode): number {
    let r = this.defaultRadius;
    if (node.properties && node.properties.radius) {
      r = node.properties.radius;
    }
    return r * this.renderer.pixelRatio;
  };

  drawNodePoint(context: CanvasRenderingContext2D, pinchZoom: any, node: TripNode) {
    if (node.properties && node.properties.point == false) {
      return;
    }
    let pos = node.viewerPos;
    this.drawPoint(context, pos, this.renderer.pixelRatio, node.properties || {});
  }

  drawNodeLabel(context: CanvasRenderingContext2D, pinchZoom: any, node: TripNode) {
    if (node.label || node.labelIcon) {
      let pos = node.labelPixelPos(pinchZoom, this.renderer.pixelRatio);
      if (node.label) {
        TripGraphLayer.drawText(context, node.label, pos,
                 this.renderer.pixelRatio, node.properties || {},
                 this.defaultTextProp);
      }
      if (node.labelIcon) {
        const icon = this.getIcon(node.labelIcon.url);
        if(icon) {
          if (node.labelIcon.autorotate != undefined) {
            let coord = pinchZoom.viewerPosFromWorldPos(node.coord);
            node.labelIcon.angle = node.labelIcon.autorotate + Math.atan2(
                coord.y - pos.y,
                coord.x - pos.x);
          }
          this.drawIcon(context, icon, node.labelIcon,
                        pos, this.renderer.pixelRatio);
        } else {
          this.missingData++;
          this.loadIcons(() => this.renderer.refresh());
        }
      }
      if (node.properties && node.properties.frame && node.label) {
        let size = TripGraphLayer.measureText(
            context, node.label,
            this.renderer.pixelRatio, node.properties || {},
            this.defaultTextProp);
        context.strokeStyle = node.properties.frame;
        size.width += 3 * this.renderer.pixelRatio;
        size.height += 3 * this.renderer.pixelRatio;
        context.rect(pos.x - size.width / 2,
                     pos.y - size.height / 2,
                     size.width, size.height);
        context.stroke();
      }
    }
  }

  setDashStyle(context: CanvasRenderingContext2D, dashed?: number[]) {
    let scaled = [];
    if (dashed) {
      for (let entry of dashed) {
        scaled.push(this.renderer.pixelRatio * entry);
      }
    }

    context.setLineDash(scaled);
  }

  drawLeaderLine(context: CanvasRenderingContext2D, pinchZoom: any, node: TripNode) {
    if (!node.properties) {
      return;
    }
    const prop = node.properties;
    if (prop.leaderLineAnchor || prop.leaderLineAnchorPixelOffset) {
      let pixelRatio = this.renderer.pixelRatio;
      let p = node.leaderLineAnchorPos(pinchZoom, pixelRatio);
      context.lineWidth = (node.properties.leaderLineWidth || 1) * pixelRatio;
      context.beginPath();

      let pos = pinchZoom.viewerPosFromWorldPos(node.coord);
      let radius = this.nodeRadius(node);

      const maxLength =
        (node.properties
         && node.properties.maxLeaderLineLength !== undefined
         ? node.properties.maxLeaderLineLength * pixelRatio : 3 * radius); 

      if (Point.dist(p, pos) > maxLength) {
        if (node.properties.dashed) {
          this.setDashStyle(context, node.properties.dashed);
        }

        context.moveTo(p.x, p.y);
        context.lineTo(pos.x, pos.y);
        context.strokeStyle = node.properties.leaderLineColor || '#000000';
        context.stroke();
        if (node.properties.dashed) {
          context.setLineDash([]);
        }
      }
    }
  };

  drawPoint(context: CanvasRenderingContext2D, p: IPoint,
            pixelRatio: number, properties: TripNodeProperties) {
      context.strokeStyle = "rgba(0,0,0,1)";
      context.fillStyle = "rgba(255,255,255,1)";
      context.lineWidth = 2 * pixelRatio;
      context.beginPath();
      const radius = (properties.radius || this.defaultRadius) * pixelRatio;
      context.arc(p.x, p.y, radius, 0, 2 * Math.PI, false);
      context.stroke();
      context.fill();

      let style = properties.markStyle;
      if (!style && properties.firstVisited) {
        style = 'first';
      }
      if (style === 'first') {
        context.fillStyle = "rgba(0, 0, 0, 1)";
        context.beginPath();
        context.arc(p.x, p.y, radius * .6, 0, 2 * Math.PI, false);
        context.fill();
      }
  };

  static measureText(context: CanvasRenderingContext2D, text: string,
                     pixelRatio: number, properties: TripNodeProperties,
                     defaultTextProp: TripNodeProperties) {
    let lines = text.split('\n');
    let size = TripGraphLayer.setTextStyle(
        context, pixelRatio, properties, defaultTextProp);

    // Because we can't measure text exactly in mapbox gl js, we add a margin.
    // It is no big deal to overestimate width.
    const expandFactor = 1.1;

    let r = { width: 0, height: size * lines.length * expandFactor };

    for (let i in lines) {
      r.width = Math.max(r.width, expandFactor * context.measureText(lines[i]).width);
    }

    return r;
  }

  static setTextStyle(context: CanvasRenderingContext2D, pixelRatio: number,
                     properties: TripNodeProperties,
                     defaultTextProp: TripNodeProperties): number {
    properties = properties || {};
    let fontSize = properties.fontSize || defaultTextProp.fontSize;
    fontSize *= pixelRatio;
    context.font = fontSize + 'px ' + (properties.font || "'PT Sans', sans-serif");
    context.strokeStyle = properties.stroke || defaultTextProp.stroke ;
    context.lineWidth = (properties.haloWidth || 4) * pixelRatio;
    context.fillStyle = properties.fill || defaultTextProp.fill;
    return parseInt(''+ fontSize);
  };

  static drawText(context: CanvasRenderingContext2D, text: string,
                  pos: IPoint, pixelRatio: number,
                  properties: TripNodeProperties,
                  defaultTextProp: TripNodeProperties) {
    let offset = (properties.textOffset != undefined ? properties.textOffset: 20)
      * pixelRatio;

    let dx, dy;
    let placement = ((properties.textPlacement || 'S') + '').toUpperCase();

    // horizontal settings
    switch (placement) {
      default:
      case 'C':
      case 'N':
      case 'S':
        context.textAlign = 'center';
        dx = 0;
        break;

      case 'E':
      case 'NE':
        context.textAlign = 'start';
        dx = offset;
        break;

      case 'O':
      case 'W':
        context.textAlign = 'end';
        dx = -offset;
        break;
    }

    // vertical settings
    switch (placement) {
      case 'C':
      case 'E':
      case 'W':
      case 'O':
        context.textBaseline = 'middle';
        dy = 0;
        break;

      case 'N':
      case 'NE':
        context.textBaseline = 'bottom';
        dy = -offset;
        break;

      default:
      case 'S':
        context.textBaseline = 'top';
        dy = offset;
        break;
    }
    let x: number = pos.x + dx;
    let y: number = pos.y + dy;

    let h: number = TripGraphLayer.setTextStyle(
        context, pixelRatio, properties, defaultTextProp);

    let lines = text.split('\n');

    context.shadowBlur = 3 * pixelRatio;
    context.shadowColor = '#ffffff';

    context.lineWidth = 2 * pixelRatio;

    let vshift: number = (lines.length - 1) / 2;
    for (let i = 0 ; i < lines.length; ++i) {
      let line_y: number = y + (i - vshift) * h;
      context.strokeText(lines[i], x, line_y);
      context.fillText(lines[i], x, line_y);
    }

    context.shadowColor = 'transparent';
  }

  drawIcon(context:CanvasRenderingContext2D, iconData: any, icon: any,
           pos: IPoint, pixelRatio:number) {
    if (!iconData) {
      return;
    }
    let width = (icon.width ? icon.width * pixelRatio: 16 * pixelRatio);
    let height = (icon.height ? icon.height * pixelRatio: width);
    let ratioX = 'ratioX' in icon ? icon.ratioX : .5;
    let ratioY = 'ratioY' in icon ? icon.ratioY : .5;
    let dx = width * ratioX;
    let dy = height * ratioY;
    if (icon.angle) {
      context.save();
      context.translate(pos.x, pos.y);
      context.rotate(icon.angle);
      context.drawImage(iconData, -dx, -dy, width, height);
      context.restore();

      this.markedPlaces.push(Point.plus(pos, Point.rotate(
                  new Point(width/2 - dx, height/2 - dy), icon.angle)));
    } else {
      context.drawImage(iconData, pos.x - dx, pos.y - dy, width, height);
      this.markedPlaces.push({
        x: pos.x - dx + width / 2,
        y: pos.y - dy + height / 2
      });
    }
  }

  getLabelIconSize(context: CanvasRenderingContext2D, node: TripNode): Size2D {
    let pixelRatio = this.renderer.pixelRatio;
    let labelIcon = node.labelIcon;

    return {
      width: (labelIcon.width || 16) * pixelRatio,
      height: (labelIcon.height || labelIcon.width || 16) * pixelRatio,
    };
  };

  computeNodeSizeWithMargin(context: CanvasRenderingContext2D, node: TripNode): Size2D {
    let result;
    if (node.label) {
      let pixelRatio = this.renderer.pixelRatio;
      result = TripGraphLayer.measureText(context, node.label, pixelRatio,
                                        node.properties, this.defaultTextProp);
    } else if (node.labelIcon) {
      result = this.getLabelIconSize(context, node);
      result.width *= 1.4;
      result.height *= 1.4;
    }
    if (!result) {
      throw('no label and no labIcon for node');
    }
    const margin = 2 * this.renderer.pixelRatio;
    result.width += 2 * margin;
    result.height += 2 * margin;
    return result;
  }

  setLabelBbox(node: TripNode, context?: CanvasRenderingContext2D,
               entry?: {x: number, y: number, width: number, height: number}) {
    if(!node
       || (!node.label && !node.labelIcon)
       || !node.properties
       || (!node.properties.labelCoord && !node.properties.labelPixelOffset)) {
      return;
    }

    if (!context) {
      context = this.renderer.canvas.getContext('2d');
    }

    const pinchZoom = this.renderer.pinchZoom;
    if (!entry) {
      const size = this.computeNodeSizeWithMargin(context, node);
      const pos = node.labelPixelPos(pinchZoom, this.renderer.pixelRatio);
      entry = {
        x: pos.x,
        y: pos.y,
        width: size.width,
        height: size.height
      };
    }

    let properties = node.properties;
    let halfSize = Point.times(1/2, new Point(entry.width, entry.height));
    properties.labelBbox = new BoundingBox({
      min: pinchZoom.worldPosFromViewerPos(Point.minus(entry, halfSize)),
      max: pinchZoom.worldPosFromViewerPos(Point.plus(entry, halfSize))
    });
  }

  nodeName(node: TripNode) {
    if (node.label) {
      return node.label;
    } else if (node.labelIcon) {
      return 'icon:' + node.labelIcon.url;
    }
    throw('no label and no labIcon for node');
  }


  // Modify TripGraph to place labels at appropriate places
  placeLabels(context: CanvasRenderingContext2D, preferLastPosition: boolean) {
    let pinchZoom = this.renderer.pinchZoom;
    let pixelRatio = this.renderer.pixelRatio;
    let labelArray = [];
    let anchorArray = [];
    for (let i in this.graph.nodes) {
      let node = this.graph.nodes[i];
      if (!node.label && !node.labelIcon) {
        continue;
      }
      let pos = pinchZoom.viewerPosFromWorldPos(node.coord);
      let radius = this.defaultRadius;
      if (node.properties && node.properties.radius) {
        radius = node.properties.radius;
      }
      anchorArray.push({
        x: pos.x,
        y: pos.y,
        r: (node.properties.point !== false ? radius * pixelRatio : -1)
      });
      let size = this.computeNodeSizeWithMargin(context, node);
      let name = this.nodeName(node);

      let initialPos = pos;
      if (node.properties.point === false) {
        initialPos.x -= size.width / 2;
        initialPos.y -= size.height / 2;
      }

      let userPlacement = undefined;
      if (preferLastPosition) {
        userPlacement = initialPos;
      }
      if (node.properties && node.properties.userPlacement) {
        userPlacement = initialPos = pinchZoom.viewerPosFromWorldPos(
            node.properties.userPlacement);
      }
      if (node.properties && node.properties.userPlacementPixelOffset) {
        userPlacement = initialPos =
          Point.plus(pos, Point.times(pixelRatio, node.properties.userPlacementPixelOffset));
      }

      const newLabel = {
        x: initialPos.x,
        y: initialPos.y,
        name: name,
        width: size.width,
        height: size.height,
        node: node,
        userPlacement: userPlacement
      };

      labelArray.push(newLabel);
    }
    let labeler = d3.labeler()
      .label(labelArray)
      .anchor(anchorArray)
      .width(this.renderer.canvas.width)
      .height(this.renderer.canvas.height);

    let originalEnergy = labeler.alt_energy();
    let me = this;
    let bezierBbox = [];
    let bezierLUT = [];
    for (let e in this.graph.edges) {
      const route = this.graph.getEdgeRoute(this.graph.edges[e]);
      if (route) {
        let points = [];
        for (let p of route.geom) {
          const pViewer = pinchZoom.viewerPosFromWorldPos(p);
          if (points.length == 0 || 
              Point.dist(points[points.length - 1], pViewer) > 4 * pixelRatio) {
            points.push(pViewer);
          }
        }
        const bbox = new BoundingBox();
        bbox.addArray(points);

        bezierBbox.push(bbox);
        bezierLUT.push(points);
      } else {
        // Bezier or straight cases
        let curves = this.graph.bezier(this.graph.edges[e]);
        for (let i in curves) {
          let curve = curves[i];
          let bbox = curve.bbox();
          bezierBbox.push({
            min: pinchZoom.viewerPosFromWorldPos(bbox.x.min, bbox.y.min),
            max: pinchZoom.viewerPosFromWorldPos(bbox.x.max, bbox.y.max)
          });
          let lut = curve.getLUT(30);
          let transformedLut = [];
          for (let i in lut) {
            transformedLut.push(pinchZoom.viewerPosFromWorldPos(lut[i]));
          }
          bezierLUT.push(transformedLut);
        }
      }
    }

    labeler.alt_energy(function(index, lab, anc) {
      let curveOverlaps = 0;

      let box = labeler.boxOfLabel(index);
      let labBbox = new BoundingBox();
      labBbox.addPoint(new Point(box.left, box.top));
      labBbox.addPoint(new Point(box.right, box.bottom));

      for (let i in bezierBbox) {
        if (!BoundingBox.overlaps(bezierBbox[i], labBbox)) {
          continue;
        }

        let lut = bezierLUT[i];
        for (let j in lut) {
          if (labBbox.contains(lut[j])) {
            curveOverlaps++;
          }
        }
      }
      let energy = curveOverlaps * 10 + originalEnergy(index);

      const userPlacement = labelArray[index].userPlacement;
      if (userPlacement) {
        const userPrefEnergy = Point.dist(userPlacement, labelArray[index]);
        energy += userPrefEnergy;
      }

      return energy;
    });

    let finalEnergy = labeler.start(5000, 1);

    for (let i in labelArray) {
      let entry = labelArray[i];
      if (!entry.node.properties) {
        entry.node.properties = {};
      }
      const properties = entry.node.properties;

      const nodePos = pinchZoom.viewerPosFromWorldPos(entry.node.coord);
      //properties.labelCoord = pinchZoom.worldPosFromViewerPos(entry);
      properties.labelPixelOffset = Point.minus(entry, nodePos).mul(1/pixelRatio);
      if ('labelCoord' in properties) {
        delete properties.labelCoord;
      }

      this.setLabelBbox(entry.node, context, entry);

      TripGraph.placeLeaderLine(entry.node, pinchZoom, pixelRatio);
      properties.textPlacement = 'C';
      properties.textOffset = 0;
    }
  }

  static shallowCopy( original: any ): any {
      // First create an empty object with
      // same prototype of our original source
      let clone = Object.create( Object.getPrototypeOf( original ) ) ;

      let i , keys = Object.getOwnPropertyNames( original ) ;

      for ( i = 0 ; i < keys.length ; i ++ )
      {
          // copy each property into the clone
          Object.defineProperty( clone , keys[ i ] ,
              Object.getOwnPropertyDescriptor( original , keys[ i ] )
          ) ;
      }

      return clone ;
  }

  forEachNode(cb: (node: TripNode) => void) {
    for (let i in this.graph.nodes) {
      cb(this.graph.nodes[i]);
    }
  }

  getIcon(url) {
    if (!this.icons[url]
        || this.icons[url].loading) {
      return undefined;
    }
    return this.icons[url];
  }

  loadIcons(cb: (err?: Error) => void) {
    if (this.iconsToLoad > 0) {
      return;
    }

    const me = this;
    this.iconsToLoad = 1;
    this.failedIcons = 0;
    const iconLoaded = function() {
      me.iconsToLoad--;
      if (cb && me.iconsToLoad === 0) {
        if (me.failedIcons > 0) {
          cb(new Error(me.failedIcons + ' icons failed to load.'));
        } else {
          cb();
        }
      }
    };

    this.forEachNode((node) => {
      if (node.labelIcon) {
        const url = node.labelIcon.url;
        if (me.icons[url]) {
          return;
        }
        me.icons[url] = { loading: true };
        me.iconsToLoad++;

        const prefix = (window as any).imageUrlPrefix || '';
        me.renderer.loadImage(prefix + node.labelIcon.url,
          function(data) {
            me.icons[url] = data;
            iconLoaded();
          },
          function(err) {
            console.log('Failed to load: ' + url + ' (prefix: ' + prefix + ')');
            me.failedIcons++;
            iconLoaded();
          }
        );
      }
    });

    iconLoaded();
  }

  findLabelAtWorldPos(pos: IPoint): TripNode {
    const nodes = this.graph.nodes;
    for (let i in nodes) {
      if (nodes.hasOwnProperty(i)) {
        let node = this.graph.nodes[i];
        if (node.properties
            && (node.properties.labelCoord || node.properties.labelPixelOffset)) {
          this.setLabelBbox(node);
        }
        if (node.properties && node.properties.labelBbox
            && BoundingBox.contains(node.properties.labelBbox, pos)) {
          return node;
        }
      }
    }
    return undefined;
  }

  findNodePointAtViewerPos(pos: IPoint): TripNode {
    const graph = this.graph;
    for (let i in graph.nodes) {
      const node = graph.nodes[i];

      if (node.properties && node.properties.point == false) {
        continue;
      }

      const properties = node.properties;
      const radius = (properties.radius || this.defaultRadius) * this.renderer.pixelRatio;
      if (Point.dist(node.viewerPos, pos) < radius) {
        return node;
      }
    }
    return undefined;
  }

  fillClicMap(canvas: HTMLCanvasElement, pinchZoom: PinchZoom) {
    const elem = document.getElementById('image-clic-map');
    const graph = this.graph;

    if (!elem) {
      return;
    }

    let html = '';
    for (let i in graph.nodes) {
      const node = graph.nodes[i];

      if (!node.label) {
        continue;
      }

      const size = this.computeNodeSizeWithMargin(canvas.getContext('2d'), node);
      const pos = node.labelPixelPos(pinchZoom, this.renderer.pixelRatio);
      const coords = [
        pos.x - size.width / 2,
        pos.y - size.height / 2,
        pos.x + size.width / 2,
        pos.y + size.height / 2,
      ].map((x) => Math.round(x));
      if (coords[0] >= canvas.width
          || coords[1] >= canvas.height
          || coords[2] < 0
          || coords[3] < 0) {
        continue;
      }
      html += '<area'
                  +' href="' + (node.dbid || node.name) +'"'
                  + ' shape="rect"'
                  + ' alt="' + node.label + '"'
                  + ' coords="' + coords.join(',') + '"'
                  + '>\n';
    }
    elem.innerHTML = html;
  }
};



