import { T, Location, CanvasTilesRenderer } from './tiles';
import { Input, Component, ViewChild, ElementRef, HostListener } from '@angular/core';
import { SeqTrip } from './seq-trip';
import { TripGraphLayer } from './trip-graph-layer';
import { TripGraph } from './trip-graph';
import { MapView2, klokanApiKey } from './mapview2.component';
import { Point } from './point';

// Mapbox GL JS can be imported by node_modules, but it causes problems
// because it gets compiled in vendor.js AND it is included in
// app/views/layouts/application.html.erb.
// Instead, we declare an external variable and skip type checking.
// Additionnally, see https://github.com/angular/angular-cli/issues/5804
//import { AttributionControl, Map, Style, Layer } from 'mapbox-gl';
declare var mapboxgl: any;
const AttributionControl = mapboxgl.AttributionControl;
const Map = mapboxgl.Map;

declare var Utils: any;

export interface PrepareCanvasOptions {
  onLocationChange?: (renderer: CanvasTilesRenderer) => void,
  created?: (renderer: CanvasTilesRenderer, tripLayer: TripGraphLayer) => void,
  alreadyCreated?: (renderer: CanvasTilesRenderer, tripLayer: TripGraphLayer) => void,
  graph?: TripGraph,
  trip: SeqTrip,
  disableMouseWheel?: boolean
};

function locationEquals(a: Location, b: Location) {
  const dx = a.x - b.x;
  const dy = a.y - b.y;
  const ds = a.scale - b.scale;

  const eps = 1e-7;

  return (Math.abs(dx) < eps
    && Math.abs(dy) < eps
    && Math.abs(ds) < eps);
}

@Component({
  selector: 'mapview',
  templateUrl: 'mapview.component.html',
  //styleUrls: ['./mapview.component.css'],
})
export class MapView {
  @Input() canvasWidth?: number;
  @Input() canvasHeight?: number;
  @Input() compactAttribution?: boolean;
  @Input() lang: string;

  // The canvas on which the overlay is displayed. Either hidden and rendered
  // by mapboxgl js, or visible on top of the map (when map repeats).
  @ViewChild('renderCanvas') renderCanvas: ElementRef;

  // This canvas will remain transparent. Its goal is to catch mouse/touch
  // events.
  @ViewChild('eventCanvas') canvas: ElementRef;
  @ViewChild('mapview') mapview : MapView2;

  private _renderer: CanvasTilesRenderer;
  private _map: any;
  private _tripLayer: TripGraphLayer;
  private waitingList : PrepareCanvasOptions[] = [];
  private _seqTrip?: SeqTrip;
  private _showOld: boolean = true;
  private _options: PrepareCanvasOptions;
  private _canvasWorldCoord: Point[];
  private _removeMapLabels: boolean = false;

  public onMapReady?: () => void;

  constructor(private el:ElementRef){
  }

  pixelRatio() : number {
    if (this._renderer && this._renderer.pixelRatio) {
      return this._renderer.pixelRatio;
    }
    return 1;
  }

  getCanvasWidth(): number {
    if (this.canvasWidth) {
     return this.canvasWidth;
    }
    if (this.el && this.el.nativeElement) {
      return this.el.nativeElement.offsetWidth;
    }
    return 0;
  }

  getCanvasHeight(): number {
    if (this.canvasHeight) {
     return this.canvasHeight;
    }
    if (this.el && this.el.nativeElement) {
      return this.el.nativeElement.offsetHeight;
    }
    return 0;
  }

  isReady() : boolean {
    const map = this._map;
    return map && this._renderer && this._tripLayer
      && this._tripLayer.isReady() && map.loaded()
      && !(map._sourcesDirty || map._repaint || map._styleDirty || map._placementDirty);
  }

  prepareCanvas(options: PrepareCanvasOptions) {
    this._options = options;
    if (!this.canvas || !this.canvas.nativeElement || this._renderer || this._map) {
      if (options.alreadyCreated) {
        if (this._map.loaded()) {
          options.alreadyCreated(this._renderer, this._tripLayer);
        } else {
          this.waitingList.push(options);
        }
      }
      return;
    }

    this._map = this.mapview.createMap(options.trip);

    this._map.on('load', () => {
      const canvas = this.canvas.nativeElement;
      const location = options.trip.location();

      this._renderer = new T.CanvasTilesRenderer({
        canvas: canvas,
        width: 1,
        clearBorder: false,
        downsampleDuringMotion: false,
        fillScreen: false,
        downgradeIfSlowerFPS: 1e-8,
        url: 'https://tiles.wmflabs.org/bw-mapnik/$scale/$x/$y.png',
        initialLocation: location,
        onLocationChange: (renderer: CanvasTilesRenderer) => {
          const newLocation = renderer.getLocation();
          this.setMapboxLocation(newLocation);
          if (options.onLocationChange) {
            options.onLocationChange(renderer);
          }
        }
      });

      this._renderer.pinchZoom.maxScale = this.getScaleFromZoom(0);

      if (options.disableMouseWheel) {
        this._renderer.pinchZoom.handleMouseWheel = function(event) { };
      }

      this._renderer.layers = [
        {
          draw: () => {
            const canvas = this.renderCanvas.nativeElement;
            const baseCanvas = this.canvas.nativeElement;
            canvas.width = baseCanvas.width;
            canvas.height = baseCanvas.height;

            const context = canvas.getContext('2d');
            context.clearRect(0, 0, canvas.width, canvas.height);
          }
        }
      ];

      this._tripLayer = new TripGraphLayer({
        renderer: this._renderer,
        graph: options.graph
      });
      
      this._renderer.addLayer(this._tripLayer);
      const oldDraw = this._tripLayer.draw;
      const zis = this;
      this._tripLayer.draw = function() {
        this.hideLabels = !zis._removeMapLabels;
        this.forceLeaderLines = true;
        if (zis._showOld) {
          const canvas = zis.renderCanvas.nativeElement;
          arguments[0] = canvas;
          oldDraw.apply(this, arguments);
          const w = canvas.width;
          const h = canvas.height;
          zis._canvasWorldCoord = [[0, 0], [w, 0], [w, h], [0, h]].map(
              (p) => zis._renderer.pinchZoom.worldPosFromViewerPos({x: p[0], y: p[1]})
            );
          zis.updateCanvas();
        }
      };

      if (options.created) {
        options.created(this._renderer, this._tripLayer);
      }
      for (let opt of this.waitingList) {
        if (opt.alreadyCreated) {
          opt.alreadyCreated(this._renderer, this._tripLayer);
        }
      }

      // Let the canvas draw itself properly before activating
      // it on mapboxgl map.
      setTimeout(()=>{
        this.setSeqTrip(options.trip);
        if (location) {
          this._removeMapLabels = location.scale > 1;
          this.switchCanvasVisibility(this._removeMapLabels);
          this.mapview.switchCustomLayers(!this._removeMapLabels);
        }
      }, 10);

      this.resize();
    }); // on map loaded

    this._map.on('idle', () => {
      const map = this._map;
      if (this.isReady()) {
        if (this.onMapReady) {
          this.onMapReady();
        }
      } else {
        console.log('mapbox map is ready, but not mapview!!');
      }
    });

  }

  getLayer(id: string) : any {
  }

  setLayerColors(seqTrip: SeqTrip) {
    // Changing map colors is disabled for now.
    /*
      let layer = this.getLayer('water');
      if (layer) {
        layer.paint['fill-color'] = seqTrip.seaColor();
      }
      layer = this.getLayer('background');
      if (layer) {
        layer.paint['background-color'] = seqTrip.landColor();
      }
      layer = this.getLayer('boundary_2');
      if (layer) {
        layer.paint['line-color'] = seqTrip.borderColor();
      }

      this._map.setStyle(this._mapStyle);
    */
  }

  resize() {
    this.mapview.resize();
    if (this._renderer) {
      this._renderer.pinchZoom.maxScale = this.getScaleFromZoom(0);
      this.setMapboxLocation(this._renderer.getLocation());
    }
  }

  setMapboxLocation(loc: Location) {
    this.mapview.setMapboxLocation(loc);

    this._removeMapLabels = loc.scale > 1;
    this.switchCanvasVisibility(this._removeMapLabels);
    this.mapview.switchCustomLayers(!this._removeMapLabels);
  }

  getZoomFromLocation(loc?: Location): number {
    if (loc) {
      // 512 * 2^z = world size
      // width / 512 = num tiles in view
      // s = s_window / (width/512) = scale of 1 tile
      // s = 1/(2^z)
      // 1/s = 2^z
      // log2(1/s) = z
      const width = this.getCanvasWidth();
      return Math.log2(1 / (loc.scale * ( 512 / width)));
    }
    return 8;
  }

  getScaleFromZoom(zoom) {
      const width = this.getCanvasWidth();
      return (width / 512) * 1 / Math.pow(2, zoom);
  }

  setSeqTrip(seqTrip: SeqTrip) : TripGraph {
    this._seqTrip = seqTrip;
    let graph;
    if (this._tripLayer.graph && Object.keys(this._tripLayer.graph.nodes).length > 0) {
      graph = this._tripLayer.graph;
    } else {
      graph = seqTrip.makeGraph(this._tripLayer);
      this._tripLayer.graph = graph;
    }

    this._tripLayer.defaultRadius = seqTrip.defaultRadius();
    this._tripLayer.defaultTextProp = seqTrip.defaultTextProp();
    this._tripLayer.defaultEdgeProp = seqTrip.defaultEdgeProp();
    const loc = seqTrip.location();
    if (!locationEquals(loc, this._renderer.location)) {
      // Avoid triggering onLocationChange
      this._renderer.setLocation(loc);
    }
    this.setMapboxLocation(loc);
    this.setLayerColors(seqTrip);
    this._renderer.refresh();
    this.resize();

    this._tripLayer.loadIcons((err) => {
      if (err) {
        console.log(err);
      }
      this._renderer.refresh();
    });
    const canvas = this.renderCanvas.nativeElement;

    const w = canvas.width;
    const h = canvas.height;
    this.mapview.addTripLayers(seqTrip, graph, canvas,
      [[0, 0], [w, 0], [w, h], [0, h]].map(
        (p) => this._renderer.pinchZoom.worldPosFromViewerPos({x: p[0], y: p[1]})
      ),
      this._tripLayer.markedPlaces
    );

    return graph;
  }

  updateCanvas() {
    if (!this._canvasWorldCoord) {
      return;
    }

    const canvas = this.renderCanvas.nativeElement;
    const w = canvas.width;
    const h = canvas.height;
    this.mapview.updateCanvasLayer(
        canvas, this._canvasWorldCoord, this._tripLayer.markedPlaces);
  }

  refresh() {
    if (this._renderer) {
      this._renderer.refresh();
    }
    if (this._seqTrip) {
      this.setSeqTrip(this._seqTrip);
    }
  }

  attribution() : string[] {
    const map = this._map;
    if (!map || !map.style) return [];
    var attributions = [];

    const sourceCaches = map.style.sourceCaches;
    for (const id in sourceCaches) {
        const sourceCache = sourceCaches[id];
        if (sourceCache.used) {
            const source = sourceCache.getSource();
            if (source.attribution && attributions.indexOf(source.attribution) < 0) {
                attributions.push(source.attribution);
            }
        }
    }

    // remove any entries that are substrings of another entry.
    // first sort by length so that substrings come first
    attributions.sort((a, b) => a.length - b.length);
    attributions = attributions.filter((attrib, i) => {
        for (let j = i + 1; j < attributions.length; j++) {
            if (attributions[j].indexOf(attrib) >= 0) { return false; }
        }
        return true;
    });

    return attributions;
  }

  switchCanvasVisibility(onoff) {
     const canvas = this.renderCanvas.nativeElement;
     const parent = canvas.parentElement;
     if (onoff) {
       parent.insertBefore(canvas, this.canvas.nativeElement);
     } else {
       parent.insertBefore(canvas, parent.firstChild);
     }
  }

  addMarker(p: { latitude: number, longitude: number }, el: any) : any {
    if (!this._map) {
      return undefined;
    }
    const marker = new mapboxgl.Marker(el)
      .setLngLat([p.longitude, p.latitude])
      .addTo(this._map);
    return marker;
  }

  addAttributionControl(compact: boolean) {
    if (!this._map) {
      return undefined;
    }
    this._map.addControl(new AttributionControl({ compact: compact }));
  }
}



