import { Point } from './point';

declare var $: any;

export interface LatLon {
  lat: number;
  lon: number;
};

let cache : { [key: string]: Promise<number[][]> }  = { };

function getProfile(mean: string) : string | undefined {
  switch(mean) {
    case 'Car': return 'driving-car';
    case 'Bus': return 'driving-hgv';
    case 'Bike': return 'cycling-regular';
    case 'Walk': return 'foot-walking';
    default: return undefined;
  }
}

function getClosestAddress(location: LatLon): Promise<LatLon> {
  const url = [
    'https://api.openrouteservice.org/geocode/reverse?'
    + 'api_key=5b3ce3597851110001cf6248e0fee72951d5494c8b4cf5d8b4ff2b76',
    'point.lat=' + location.lat,
    'point.lon=' + location.lon,
    'sources=oa'
  ].join('&');

  return new Promise((resolve, reject) => {
    $.getJSON(url)
    .done((data) => {
      if (data && data.features && data.features[0] && data.features[0].geometry
          && data.features[0].geometry.coordinates) {
        resolve({lon: data.features[0].geometry.coordinates[0], lat: data.features[0].geometry.coordinates[1]} as LatLon)
      } else {
        reject(new Error('Empty or bad data from openrouteservice'));
      }
    })
    .fail((err) => {
       if (typeof err == 'object'
           && typeof err.status == 'number'
           && err.status >= 400 && err.status < 500) {
         // unrecoverable error. We should forget the idea of having a route
         // for this edge.
         reject(err);
       }
       // TODO try again later.
       reject(err);
    });
  });
}

function distance(a: LatLon, b: LatLon) {
  const dx = a.lat - b.lat;
  const dy = a.lon - b.lon;
  return Math.sqrt(dx * dx + dy * dy);
}

function findClosest(reference: LatLon, locations: LatLon[]): LatLon {
  let bestDist;
  let bestLoc;
  for (let l of locations) {
    const dist = distance(reference, l);
    if (bestDist == undefined || dist < bestDist) {
      bestDist = dist;
      bestLoc = l;
    }
  }
  return bestLoc;
}

/* conditions to use overpass-api:
 *
 * Any of the three servers has a total capacity of about 1,000,000 requests
 * per day. You can safely assume that you don't disturb other users when you
 * do less than 10,000 queries per day and download less than 5 GB data per
 * day. The address overpass-api.de and endpoint
 * https://overpass-api.de/api/interpreter will continue to work and redirects
 * per round robin to z and lz4.
 *
 * we should be fine.
 * See https://wiki.openstreetmap.org/wiki/Overpass_API#Public_Overpass_API_instances
 */
async function getClosestRoad(location: LatLon): Promise<LatLon> {
  return new Promise((resolve: (loc: LatLon) => void, reject) => {
    $.getJSON(
      'http://overpass-api.de/api/interpreter?data='
      + encodeURIComponent('[out:json];('
        + 'way["highway"~"^(road|motorway|trunk|primary|secondary|tertiary|residential)$"]'
        + '["access:conditional"!~"no"]["access"!~"no"]'
        + '["name"](around:15000,' + location.lat + ',' + location.lon + ');>;);out 32;\n'))
    .done((data) => {
      if (data && Array.isArray(data.elements) && data.elements.length >= 1
          && data.elements[0].lat && data.elements[0].lon) {
        const closest = findClosest(location, data.elements);
        resolve(closest);
      } else {
        reject('Bad or empty reply from overpass');
      }
    })
    .fail(reject);
  });
}

const closestAddressCache: { [key: string]: Promise<LatLon> } = {};

function getClosestAddressOrRoad(location: LatLon): Promise<LatLon> {
  const key = JSON.stringify(location);
  if (closestAddressCache[key]) {
    return closestAddressCache[key];
  }
  return closestAddressCache[key] = (async () => {
    try {
      return await getClosestRoad(location);
    } catch(err) { /* failed, let's try another service */ }

    try {
      return await getClosestAddress(location);
    } catch(err) {
      // also failed.. but we can continue with the input location,
      // hoping that the routing service will work.
    }
    return location;
  })();
}

function fetchDirections(from: LatLon, to: LatLon, profile: string): Promise<number[][]> {
  const url = [
        'https://api.openrouteservice.org/directions?'
          + 'api_key=5b3ce3597851110001cf6248e0fee72951d5494c8b4cf5d8b4ff2b76',
        'coordinates=' + from.lon + '%2C' + from.lat + '|'
                      + to.lon + '%2C' + to.lat,
        'profile=' + profile,
        'geometry_format=geojson',
        'geometry_simplify=true',
        'radiuses=-1|-1'
      ] .join('&');
  return new Promise((resolve, reject) => {
    $.getJSON(url)
    .done((data) => {
      if (data && data.routes && data.routes[0] && data.routes[0].geometry
          && data.routes[0].geometry.coordinates) {
        resolve(data.routes[0].geometry.coordinates)
      } else {
        reject(new Error('Empty or bad data from openrouteservice'));
      }
    })
    .fail(reject);
  });
}

export function getDirections(from: LatLon, to: LatLon, mean: string, retry: boolean = false): Promise<number[][]> {
  const profile = getProfile(mean);
  if (!profile) {
    throw new Error('Transportation mean unsupported: ' + mean);
  }

  const cacheKey = JSON.stringify({ from, to, mean });

  if (cacheKey in cache) {
    return cache[cacheKey];
  }

  cache[cacheKey] = (async() => {
    let replaceFrom: boolean = false;
    let replaceTo: boolean = false;

    try {
      return await fetchDirections(from, to, profile);
    } catch(err) {
      if (typeof(err) != 'object' || !err.responseJSON
          || err.responseJSON.error.code != 2010) {
        // we propagate the error.
        throw err;
      }
      const regex = /Cannot find point ([0-9]+)/g;
      let matches;
      while (matches = regex.exec(err.responseJSON.error.message)) {
        switch(matches[1]) {
          case '0': replaceFrom = true; break;
          case '1': replaceTo = true; break;
        }
      }
      if (!replaceFrom && !replaceTo) {
        throw err;
      }
      // we may try again, replacing one of the destination.
    }

    // second attempt. Those functions always return something.
    const _from = (replaceFrom ? await getClosestAddressOrRoad(from) : from);
    const _to = (replaceTo ? await getClosestAddressOrRoad(to) : to);

 
    // This time, we're not catching the error.
    // If it fails, then.. we're out of luck.
    const directions = await fetchDirections(_from, _to, profile);
    directions.unshift([from.lon, from.lat])
    directions.push([to.lon, to.lat])

    return directions;
  })();

  return cache[cacheKey];
}

export function midPoint(points: Point[]): number {
  let length = 0;
  const lengthArray : number[] = [ 0 ];
  for (let i = 1; i < points.length; ++i) {
    const dist = Point.dist(points[i], points[i-1]);
    length += dist;
    lengthArray.push(length);
  }
  const halfDist = length / 2;
  for (let i = 0; i < lengthArray.length; ++i) {
    if (lengthArray[i] > halfDist) {
      return i - 1;
    }
  }
  return 0;
}



