import { Point, IPoint } from './point';
import { TripNode } from './trip-node';
import { TripEdge, TripEdgeProperties, RouteData } from './trip-edge';

import { BoundingBox } from './bounding-box';
import { LatLon, getDirections, midPoint } from './open-route-service';
import { PinchZoom } from './tiles';

declare var d3: any;
declare var Bezier: any;
declare var Utils: any;

export class TripGraph {
  nodes: { [name: string]: TripNode };
  edges: TripEdge[];
  defaultEdgeProp: TripEdgeProperties;
  mapStyle?: string;

  static generateControlPoints(
      p0: Point, p1: Point, p2: Point, p3: Point): Point[] {
    const delta = Point.minus(p2, p1).mul(1 / 3);
    const norm = new Point(-delta.y, delta.x);
    return [
      Point.plus(Point.plus(p1, delta), norm),
      Point.plus(Point.minus(p2, delta), norm)
    ];
  };

  static bezierFromPoints(points: IPoint[]): any {
    const result = [];
    for (let i = 0; i < 4; ++i) {
      result.push(points[i].x);
      result.push(points[i].y);
    }
    return new Bezier(result);
  };

  static bezierThroughPoints(points: IPoint[]) {
    const result = [];
    const t = .2;
    for (let i = 1; i < points.length; ++i) {
      const prev = (i === 1 ? points[i - 1] : points[i - 2]);
      const a = points[i - 1];
      const b = points[i];
      const next = (i === (points.length - 1) ? points[i] : points[i + 1]);
      result.push(TripGraph.bezierFromPoints([
        a,
        Point.plus(a, Point.times(t, Point.minus(b, prev))),
        Point.plus(b, Point.times(t, Point.minus(a, next))),
        b]));
    }
    return result;
  };

  static placeLeaderLine(node: TripNode, pinchZoom: PinchZoom, pixelRatio: number) {
    if (!node.properties) {
      node.properties = {};
    }
    const placement = node.properties.leaderLine || 'bestAnchor';

    const bbox = node.properties.labelBbox;
    let anchor;
    if (placement === 'center') {
      const  width = bbox.max.x - bbox.min.x;
      const  center = new Point((bbox.min.x + bbox.max.x) / 2,
                                (bbox.min.y + bbox.max.y) / 2);
      const  toCoord = Point.minus(node.coord, center);
      toCoord.mul(.02);
      anchor = Point.plus(center, toCoord);
    } else if (placement === 'closestOnBbox') {
      anchor = BoundingBox.closestPoint(node.coord, bbox);
    } else {
      anchor = d3.labeler.closestLineAnchorPoint(
            node.coord,
            {
              left: bbox.min.x,
              right: bbox.max.x,
              top: bbox.min.y,
              bottom: bbox.max.y,
              cx: (bbox.min.x + bbox.max.x) / 2,
              cy: (bbox.min.y + bbox.max.y) / 2
            }
        );
    }

    node.properties.leaderLineAnchorPixelOffset = undefined;
    node.properties.leaderLineAnchor = anchor;
    node.convertToPixelOffset(pinchZoom, pixelRatio);
  }
  constructor(graph: any, defaultEdgeProp: TripEdgeProperties) {
    const g = graph || {};
    this.nodes = g.node || {};
    this.edges = g.edges || [];
    this.defaultEdgeProp = defaultEdgeProp;
  }

  createDefaultBezier() {
    for (let i in this.edges) {
      const e = this.edges[i];
      const from = this.nodes[e.from];
      const to = this.nodes[e.to];

      const pts = TripGraph.generateControlPoints(undefined,
                                                  from.coord, to.coord,
                                                  undefined);
      // we replace the array in place to avoid breaking the links
      // to this object.
      for (let i = 0; i < pts.length; ++i) {
        e.controlPoints[i] = pts[i];
      }
    }
  }

  edgeProperty(edge: TripEdge, prop: string) {
    return edge.properties[prop] || this.defaultEdgeProp[prop];
  }

  getEdgeRoute(edge: TripEdge) : RouteData {
    if (this.edgeProperty(edge, 'geom') == 'route'
          && edge.properties
          && edge.properties.route
          && edge.properties.route.geom
          && this.edgeProperty(edge, 'travelMean') == edge.properties.route.mean) {
      return edge.properties.route;
    }
    return undefined;
  }

  bezier(edge: TripEdge): any[] {
    if (!edge.controlPoints || edge.properties.hidden) {
      return [];
    }

    if (this.edgeProperty(edge, 'geom') == 'straight') {
      return TripGraph.bezierThroughPoints(
          [ this.nodes[edge.from].coord, this.nodes[edge.to].coord ]);
    }

    if (edge.controlPoints.length === 2) {
      const points = [
        this.nodes[edge.from].coord,
        edge.controlPoints[0],
        edge.controlPoints[1],
        this.nodes[edge.to].coord
      ];
      return [TripGraph.bezierFromPoints(points)];
    } else if (edge.controlPoints.length === 5) {
      const points1 = [
        this.nodes[edge.from].coord,
        edge.controlPoints[0],
        edge.controlPoints[1],
        edge.controlPoints[2],
      ];
  
      const points2 = [
        edge.controlPoints[2],
        edge.controlPoints[3],
        edge.controlPoints[4],
        this.nodes[edge.to].coord
      ];
      return [
        TripGraph.bezierFromPoints(points1),
        TripGraph.bezierFromPoints(points2)
      ];
    } else {
      const allPoints = [this.nodes[edge.from].coord]
        .concat(edge.controlPoints, [ this.nodes[edge.to].coord ]);
      return TripGraph.bezierThroughPoints(allPoints);
    }
  }

  bounds(): BoundingBox {
    const r = new BoundingBox();
    for (let i in this.nodes) {
      if (!this.nodes[i].properties.skip) {
        r.addPoint(this.nodes[i].coord);
      }
    }
    for (let i in this.edges) {
      const edge = this.edges[i];
      const route = this.getEdgeRoute(edge);
      if (route) {
        for (let p of route.geom) {
          r.addPoint(p);
        }
      } else {
        const bezierCurves = this.bezier(edge);
        for (let c of bezierCurves) {
          const lut = c.getLUT(8);
          for (let p of lut) {
            r.addPoint(p);
          }
        }
      } 
    }
    return r;
  }

  // Returns a rectangle containing all the nodes with the desired aspect ratio
  // and margin factor.
  //
  // aspectRatio: width / height
  frame(aspectRatio?: number, marginRatio?: Point | number): BoundingBox {
    const bounds = this.bounds();
    marginRatio = marginRatio || 1.1;

    if (!bounds.max || !bounds.min) {
      // no point. Let's use the full world.
      bounds.min = new Point(0, 0);
      bounds.max = new Point(1, 1);
      marginRatio = 1;
    } else if (bounds.min.x === bounds.max.x) {
      // degenerate case, most likely becase was only 1 point. We need an area
      // to define a frame.
      const delta = new Point(1/32, 1/32);
      bounds.addPoint(Point.plus(bounds.min, delta));
      bounds.addPoint(Point.minus(bounds.min, delta));
    }

    aspectRatio = aspectRatio
      || (bounds.max.x - bounds.min.x) / (bounds.max.y - bounds.min.y);

    const margin = (typeof(marginRatio) === 'number' ?
                  new Point(marginRatio, marginRatio) : marginRatio);

    const initialSize = Point.minus(bounds.max, bounds.min);
    initialSize.x *= margin.x;
    initialSize.y *= margin.y;

    const widthFromHeight = initialSize.y * aspectRatio;

    let size;
    if (initialSize.x > widthFromHeight) {
      size = new Point(initialSize.x, initialSize.x / aspectRatio);
    } else {
      size = new Point(initialSize.y * aspectRatio, initialSize.y);
    }

    size.mul(.5);
    const center = Point.times(0.5, Point.plus(bounds.max, bounds.min));
    const result = new BoundingBox();
    result.addPoint(Point.minus(center, size));
    result.addPoint(Point.plus(center, size));
    return result;
  }

  location(aspectRatio?: number, marginRatio?: Point | number) {
    const frame = this.frame(aspectRatio, marginRatio);
    const dx = frame.max.x - frame.min.x;
    return {
      x: (frame.max.x + frame.min.x) / 2,
      y: (frame.max.y + frame.min.y) / 2,
      scale: Math.max(1 / (1<<15), (frame.max.x - frame.min.x))
    };
  }

  public compareNodes(other: TripGraph) : boolean {
    const dictHasAll = (d1, d2) => {
      for (let n in d1) {
        if (!(n in d2)) {
          return false;
        }
      }
      return true;
    };

    return (dictHasAll(this.nodes, other.nodes)
            && dictHasAll(other.nodes, this.nodes));
  }

  public getEdgeDirections(edge: TripEdge, mean: string) : Promise<RouteData> {
    const from = Utils.worldToLatLon(this.nodes[edge.from].coord) as LatLon;
    const to = Utils.worldToLatLon(this.nodes[edge.to].coord) as LatLon;

    return getDirections(from, to, mean)
      .then((data: number[][]) => {
        const points = data.map((coord) => new Point(Utils.latLonToWorld(coord)));
        const route : RouteData = {
          mean: mean,
          geom: points,
          from: edge.from,
          to: edge.to,
          midpoint: midPoint(points)
        };
        return route;
      });
  }
}



