import { Location } from './tiles';
import { Input, Component, ViewChild, ElementRef, HostListener } from '@angular/core';
import { SeqTrip } from './seq-trip';
import { TripNode } from './trip-node';
import { TripEdge } from './trip-edge';
import { IPoint, Point } from './point';
import { TripGraph } from './trip-graph';

export const klokanApiKey = 'e7trAnc5053j8mX60MlQ';

// Mapbox GL JS can be imported by node_modules, but it causes problems
// because it gets compiled in vendor.js AND it is included in
// app/views/layouts/application.html.erb.
// Instead, we declare an external variable and skip type checking.
// Additionnally, see https://github.com/angular/angular-cli/issues/5804
//import { AttributionControl, Map, Style, Layer } from 'mapbox-gl';
declare var mapboxgl: any;
const AttributionControl = mapboxgl.AttributionControl;
const Map = mapboxgl.Map;
const ScaleControl = mapboxgl.ScaleControl;

declare var Utils: any;
declare var Bezier: any;


function worldToLatLon(p? : IPoint): [number] {
  if (!p) {
    return [] as [number];
  }
  const ll = Utils.worldToLatLon(p);
  return [ll.lon, ll.lat];
}

function coordinatesOfEdge(edge : TripEdge, trip: SeqTrip, graph: TripGraph) {
  const nodes = graph.nodes;
  let geom = trip.edgeProperty(edge, 'geom');

  if ((edge.properties && edge.properties.hidden)
      || nodes[edge.from].properties.skip
      || nodes[edge.to].properties.skip) {
    return [];
  }
  if (geom == 'route') {
    if (edge.properties && edge.properties.route && edge.properties.route.geom) {
      return edge.properties.route.geom.map(worldToLatLon);
    } else {
      geom = 'straight';
    }
  }
  if (geom == 'straight') {
    return [ nodes[edge.from].coord, nodes[edge.to].coord ].map(worldToLatLon);
  }

  if (edge.controlPoints.length == 2) {
    // bezier
    const ctrlPoints = [
          nodes[edge.from].coord,
          edge.controlPoints[0],
          edge.controlPoints[1],
          nodes[edge.to].coord
    ];

    const ptArray = [];
    for (let p of ctrlPoints) {
      ptArray.push(p.x);
      ptArray.push(p.y);
    }
    const bezier = new Bezier(ptArray);

    const points = bezier.getLUT(128);
    return points.map(worldToLatLon);
  }

  console.log('Unexpected edge structure, not drawing');
  return [];
}

function styleUrl(style) {
  if (style.match(/^http/)) {
    return style;
  }
  return 'https://maps.tilehosting.com/styles/' + style + '/style.json?key=' + klokanApiKey;
}

export interface PrepareCanvasOptions {
};

@Component({
  selector: 'mapview2',
  templateUrl: 'mapview2.component.html',
})
export class MapView2 {
  @Input() canvasWidth: number;
  @Input() canvasHeight: number;
  @Input() compactAttribution?: boolean;
  @Input() lang: string;

  @ViewChild('mapDiv') mapDiv: ElementRef;

  private _map: any;
  private _trip: SeqTrip;
  private _graph?: TripGraph;
  private waitingList : PrepareCanvasOptions[] = [];
  private pauseCanvasSourceTimeout?;
  private _disabledLayers?: any[];
  private _iconFactor?: number;

  pixelRatio() : number {
    return window.devicePixelRatio || 1;
  }

  createMap(trip : SeqTrip) {
    if (this._map) {
      return;
    }

    const options = {
      container: this.mapDiv.nativeElement,
      // To change the style, it is also necessary to change the name filters
      // below.
      style: styleUrl(trip.getMapStyle()), 
      center: worldToLatLon(trip.location()),
      zoom: this.getZoomFromLocation(trip.location()),
      attributionControl: false,
      minZoom: 0,
      renderWorldCopies: true
    };
    this._map = new Map(options);

    //this._map.addControl(new AttributionControl({
    //  compact: this.compactAttribution }));


    this._map.on('error', (err) => {
      console.log('Map error:', err);
    });

    this._map.on('load', () => {
      const allSource = Object.keys(this._map.getStyle().sources);

      if (this.lang) {
        this._map.setLanguage(this.lang);
      }

      for (let s of allSource) {
        const source = this._map.getSource(s);
        if (source) {
          // Boost details
          source.tileSize = 400; // instead of 512
          source.roundZoom = true;
        }
      }
    });

    const scale = new ScaleControl();
    this._map.addControl(scale);

    return this._map;
  }

  addTripLayers(trip: SeqTrip, graph: TripGraph, canvas: HTMLCanvasElement,
                coord: Point[], markedPlaces: IPoint[]) {
    const map = this._map;
    this._graph = graph;

    const dayEdgeData = this.buildPathGeoJson(trip, graph);
    const dayEdgeSource = map.getSource('dayEdges');
    const firstSymbolId = this.firstLayerOfType('symbol').id;

    if (!canvas) {
      if (dayEdgeSource) {
        dayEdgeSource.setData(dayEdgeData);
      } else {
        map.addSource('dayEdges', {
          "type": "geojson",
          "data": dayEdgeData
        });
      }
      if (map.getLayer('dayEdgesLayer') == undefined) {
        const edges = map.addLayer({
          "id": "dayEdgesLayer",
          "type": "line",
          "source": 'dayEdges',
          minZoom: 0,
          "layout": {
            "line-join": "round",
            "line-cap": "round"
          },
          "paint": {
            'line-color': ['get', 'color'],
            "line-width": ['get', 'width'],
            //'line-dasharray': [1,2]
          }
        }, firstSymbolId);
      }
    } else {
      this.updateCanvasLayer(canvas, coord, markedPlaces);
    }

    const dayPlacesData = this.buildPlacesGeoJson(trip, graph);
    const dayPlacesSource = map.getSource('dayPlaces');
    if (dayPlacesSource) {
      dayPlacesSource.setData(dayPlacesData);
      map.setLayoutProperty('dayLabels', 'icon-size', this._iconFactor);
    } else {
      map.addSource('dayPlaces', {
          "type": "geojson",
          "data": dayPlacesData
        });
      // The following makes it possible to draw place circles with mapbox.
      // However, it does not support the black dot inside the circle to
      // highlight some places. So we render using the canvas instead.
      /*
      map.addLayer({
        "id": "placesCircle",
        "type": "circle",
        "source": 'dayPlaces',
        "paint": {
          'circle-radius': 6,
          'circle-color': '#ffffff',
          'circle-stroke-width': 2,
          'circle-stroke-color': '#000000'
        },
      }, firstSymbolId);
      */

      this.genBlackDot();
      map.addLayer({
        id: 'dayLabels',
        type: 'symbol',
        source: 'dayPlaces',
        minZoom: 0,
        layout: {
            "text-field": "{label}",
            "text-font": [ 'match', [ 'get', 'fontName' ],
              'noto', [ 'literal', [ 'Noto Sans Bold' ] ],
              [ 'literal', [ 'Open Sans Semibold' ] ] ],
            "text-anchor": "center",
            "text-offset": {
               "type": "identity",
               "property": "labelOffset"
            },
            "text-size": {
              property: "fontSize",
              type: "identity"
            },
            "text-allow-overlap": true,
            "text-max-width": 100,
            "visibility": this._disabledLayers ? 'none' : 'visible',
            "icon-image": ['get', 'icon'],
            "icon-anchor": 'center',
            "icon-allow-overlap": true,
            "icon-size": this._iconFactor,
            "icon-offset": {
               "type": "identity",
               "property": "iconOffset"
            },
        },
        paint: {
            "text-color": { property: 'fill', type: 'identity' },
            "text-halo-color": { property: 'stroke', type: 'identity' },
            "text-halo-width": 1.2
        }
      });

      // TODO: hide labels dynamicaly
      const placeNames = trip.places()
          .filter((p : TripNode) => { return !p.properties.skip; })
          .map((p : TripNode) => { return p.label + ''; });
      this.hidePlaces(placeNames);
    }
  }

  updateCanvasLayer(canvas: HTMLCanvasElement, coord: Point[],
                    markedPlaces: IPoint[]) {
    if (!canvas || !coord) {
      return;
    }
    const map = this._map;
    let canvasSource = map.getSource('canvasSource');
    if (canvasSource) {
      const oldCoord = canvasSource.coordinates;
      const newCoord = coord.map(worldToLatLon);

      if (JSON.stringify(oldCoord) != JSON.stringify(newCoord)) {
        canvasSource.setCoordinates(newCoord);
      }
      canvasSource.play();
    } else {
      const firstSymbolId = this.firstLayerOfType('symbol').id;

      map.addSource('canvasSource', {
        "type": "canvas",
        "canvas": canvas,
        "animate": true,
        "coordinates": coord.map(worldToLatLon)
      });

      map.addLayer({
        id: 'canvasLayer',
        type: 'raster',
        source: 'canvasSource',
        paint: {
          'raster-fade-duration': 0
        },
        layout: {
          "visibility": this._disabledLayers ? 'none' : 'visible'
        },
        minZoom: 0,
      }, firstSymbolId);
    }
    if (this.pauseCanvasSourceTimeout) {
      clearTimeout(this.pauseCanvasSourceTimeout);
    }
    // The initial rendering might be wrong due to resize or anything else.
    // So we need to animate for a while in the beginning.
    this.pauseCanvasSourceTimeout =  setTimeout(() => {
      this.pauseCanvasSourceTimeout = undefined;
      map.getSource('canvasSource').pause();
    }, 200);

    this.killMapLabels(markedPlaces);
  }

  firstLayerOfType(type: string) : any {
    const layers = this._map.getStyle().layers;
    for (let i = 0; i < layers.length; i++) {
      if (layers[i].type === type
          // In style topo, there's a spurious layer: contour_label.
          && layers[i].id != 'contour_label'

          // In positron style, there are a few layer to ignore.
          && !layers[i].id.match(/^(water_name)|(highway)/)
          ) {
        //console.log('Layer id: ' + layers[i].id);
        return layers[i];
      }
    }
  }

  resize() {
    if (this._map) {
      this._map.resize();
      if (this._trip) {
        this.setMapboxLocation(this._trip.location());
      }
    }
  }

  setMapboxLocation(loc: Location) {
    if (!isFinite(loc.x) || !isFinite(loc.y)) {
      throw(new Error('Invalid coordinates: '
                     + loc.x + ', ' + loc.y));
    }
    if (!this._map) {
      return;
    }

    this._map.setCenter(worldToLatLon(loc));

    if (isFinite(loc.scale)) {
      this._map.setZoom(this.getZoomFromLocation(loc));
    }
  }

  getZoomFromLocation(loc?: Location): number {
    if (loc) {
      // 512 * 2^z = world size
      // width / 512 = num tiles in view
      // s = s_window / (width/512) = scale of 1 tile
      // s = 1/(2^z)
      // 1/s = 2^z
      // log2(1/s) = z
      const width = this.canvasWidth;
      return Math.log2(1 / (loc.scale * ( 512 / width)));
    }
    return 8;
  }

  private buildPathGeoJson(trip: SeqTrip, graph: TripGraph) {
    return {
      "type": "FeatureCollection",
      "features": graph.edges.map((s : TripEdge) => {
        return {
          "type": "Feature",
          "geometry": {
            "type": "LineString",
            "coordinates": coordinatesOfEdge(s, trip, graph),
          },
          "properties": {
            "mean": s.properties.travelMean,
	    "color": trip.edgeProperty(s, 'lineColor'),
	    "width": parseFloat(trip.edgeProperty(s, 'lineWidth')) /* * this.pixelRatio() */,
	    "dashed": (trip.edgeProperty(s, 'dashed') || []).concat([1])[0],
          }
        };
      })
    };
  }

  private buildPlacesGeoJson(trip: SeqTrip, graph: TripGraph) {
    let features = [];
    this._iconFactor = (trip.defaultTextProp().fontSize/12 * 16 + 4) / 64.0;
    for (let name in graph.nodes) {
      const p = graph.nodes[name]; 
      if (p.properties.skip) {
	continue;
      }

      const fontSize = (trip.nodeProperty(p, 'fontSize') || 12);
      let offset = [ 0, 0 ];
      let iconOffset = [ 0, 0 ];

      if (p.properties.labelPixelOffset) {
        // convert pixels to em units
        const conv = 1 / fontSize;

        offset = [
          p.properties.labelPixelOffset.x * conv,
          p.properties.labelPixelOffset.y * conv
        ];
        iconOffset = [
          p.properties.labelPixelOffset.x / this._iconFactor,
          p.properties.labelPixelOffset.y / this._iconFactor
        ];
      }
      features.push({
          "type": "Feature",
          "geometry": {
            "type": "Point",
            "coordinates": worldToLatLon(p.coord)
          },
          "properties": {
            "label": p.label,
            "labelOffset": offset,
            "iconOffset": iconOffset,
            "fontSize": fontSize,
            "fontName": trip.nodeProperty(p, 'blackdot') ? 'noto' : 'default',
            "icon": trip.nodeProperty(p, 'blackdot') ? 'blackdot' : '',
            "fill": trip.nodeProperty(p, 'fill'),
            "stroke": trip.nodeProperty(p, 'stroke'),
          }
        });
    }

    return {
      "type": "FeatureCollection",
      "features": features
    };
  }
 
  hidePlaces(places: string[]) {
    const hidePlacesFilters = [
      ['!in', 'name:' + this.lang].concat(places),
      ['!in', 'name'].concat(places)
    ];

    // The code below assumes the map style 'street' is applied, and has been
    // modified by the setLanguage function.
    const suffixes = ['', '-' + this.lang ];
    const map = this._map;
    ['place_city_capital', 'place_city', 'place_town', 'place_village', 'place_other']
      .forEach(function(place) {
        suffixes.forEach(function(suffix) {
          const id = place + suffix;
          if (map.getLayer(id)) {
            let filter = map.getFilter(id)
            filter = filter.concat(hidePlacesFilters);
            map.setFilter(id, filter);
          }
        });
      });
  }

  killMapLabels(markedPlaces: IPoint[]) {
    const map = this._map;

    if (!map.hasImage('blank4')) {
      const width = 4; // The image will be 4x4 pixels square
      const bytesPerPixel = 4; // Each pixel is represented by 4 bytes: red, green, blue, and alpha.
      const data = new Uint8Array(width * width * bytesPerPixel);

      for (var x = 0; x < width; x++) {
        for (var y = 0; y < width; y++) {
          var offset = (y * width + x) * bytesPerPixel;
          data[offset + 0] = 255;  // red
          data[offset + 1] = 0;    // green
          data[offset + 2] = 0;    // blue
          data[offset + 3] = 0;    // alpha
        }
      }

      map.addImage('blank4', {width: width, height: width, data: data});
    }

    if (!markedPlaces) {
      console.log('No marked places!');
    }
    let blankCoords : number[][] = markedPlaces.map(worldToLatLon);
    
    if (this._graph) {
      // Kill labels around all node centers
      blankCoords = blankCoords.concat(
          Object.keys(this._graph.nodes).map((k) => {
            const p = this._graph.nodes[k].coord;
            return worldToLatLon(p);
          }));
    }

    // Add blank coords all around the viewport to kill labels that might
    // cross the viewport boundary
    const canvas = map.getCanvas();
    const w = canvas.clientWidth;
    const h = canvas.clientHeight;

    const delta = 18;
    const borders = [];
    for (let x = delta; x < w; x+= delta) {
      borders.push(map.unproject([x, 0]));
      borders.push(map.unproject([x, h]));
    }
    for (let y = delta; y < h; y+= delta) {
      borders.push(map.unproject([0, y]));
      borders.push(map.unproject([w, y]));
    }

    blankCoords = blankCoords.concat(borders.map((x) => [x.lng, x.lat]));

    const blankData = {
      type: "FeatureCollection",
      features: blankCoords.map((x) => {
        return {
          type: "Feature",
          geometry: {
            type: "Point",
            coordinates: x
          }
        };
      })
    };

    const source = map.getSource('blankSource');
    if (!source) {
      map.addSource('blankSource', {
          "type": "geojson",
          "data": blankData
        });

      map.addLayer({
        id: 'blank',
        type: 'symbol',
        source: 'blankSource',
        minZoom: 0,
        layout: {
          "icon-image": 'blank4',
          "icon-anchor": 'center',
          "icon-size": 4,
          "icon-allow-overlap": true,
          "visibility": this._disabledLayers ? 'none' : 'visible'
        }
      });
    } else {
      source.setData(blankData);
    }
  }


  switchCustomLayers(onoff) {
    if (!this._map) {
      return;
    }
    const map = this._map;

    if (onoff && !this._disabledLayers) {
      // already enabled.
      return;
    }

    if (!onoff) {
      if (this._disabledLayers) {
        // already disabled.
        return;
      }

      this._disabledLayers = [
        'blank', 
        'dayLabels',
        'canvasLayer'];
      const layers = this._map.getStyle().layers;
      for (let i = 0; i < layers.length; i++) {
        const layer = layers[i];
        if (layer.type == 'symbol'
            && map.getLayoutProperty(layer.id, 'visibility') == 'visible') {
          this._disabledLayers.push(layer.id);
        }
      }
    }
    const visibility = (onoff ? 'visible' : 'none');
    this._disabledLayers.forEach((layerid) => {
        map.setLayoutProperty(layerid, 'visibility', visibility);
    });
    if (onoff) {
      this._disabledLayers = undefined;
    }
  }

  genBlackDot() {
    const map = this._map;
    if (!map.hasImage('blackdot')) {
      const width = 64; // The image will be 4x4 pixels square
      const bytesPerPixel = 4; // Each pixel is represented by 4 bytes: red, green, blue, and alpha.
      const data = new Uint8Array(width * width * bytesPerPixel);

      const r = (width - 1) / 2;
      const center = new Point((width - 1) / 2, (width - 1) / 2);
      for (let x = 0; x < width; x++) {
        for (let y = 0; y < width; y++) {
          const p = new Point(x, y);
          const dist = Point.dist(p, center);

          const alpha = Math.min(1, Math.max(0, r - dist));

          var offset = (y * width + x) * bytesPerPixel;
          data[offset + 0] = 0;  // red
          data[offset + 1] = 0;  // green
          data[offset + 2] = 0;  // blue
          data[offset + 3] = 255 * alpha;
        }
      }

      map.addImage('blackdot', {width: width, height: width, data: data});
    }
  }
}



