import { Point, IPoint } from './point';
import { MapParams, globalMapParams } from './map-params';
import { TripEdge, TripEdgeProperties } from './trip-edge';
import { TripGraph } from './trip-graph';
import { TripNode, TripNodeProperties, ITripNode } from './trip-node';
import { TripSaved } from './trip-saved';
import { TripGraphLayer } from './trip-graph-layer';
import { PageSize, IPageSize } from './page-size';
import * as Tiles from './tiles';

export class SeqTrip {
  public _tripData: TripSaved;

  constructor() {
    this.reset();
  }

  private static makePlaneIcon(verb: string, placeNode: TripNode): TripNode {
    const iconNode = new TripNode();
    iconNode.name = placeNode.dbid + '-' + verb,
    iconNode.labelIcon = {
        url: '/ng-map/assets/plane.png',
        width: 20,
        autorotate: (Math.PI / 2) * (verb === 'landing' ? 1 : -1),
        ratioX: .5,
        ratioY: (verb === 'landing' ? 0 : 1),
    };
    iconNode.coord = placeNode.coord;
    iconNode.properties = {
        point: false,
        leaderLine: 'center',
        dashed: [5, 3],
        leaderLineWidth: 2,
        maxLeaderLineLength: 2
    };
    return iconNode;
  }

  places(): TripNode[] {
    return this._tripData.places;
  }

  stages(): TripEdge[] {
    return this._tripData.stages;
  }

  setLocation(loc: Tiles.Location) { this._tripData.location = loc; }
  location(): Tiles.Location { return this._tripData.location; }

  width_mm(): number { return this._tripData.width; }
  height_mm(): number { return this._tripData.height; }
  pageSizeName(): string { return this._tripData.pageSizeName; }
  world(): any {
    if (!this._tripData.world) {
      this._tripData.world = {};
    }
    return this._tripData.world;
  }

  setSize(name: string, width_mm: number, height_mm: number) {
    this._tripData.width = width_mm;
    this._tripData.height = height_mm;
    this._tripData.pageSizeName = name;
  }

  defaultRadius(): number { return this._tripData.defaultRadius; }

  defaultTextProp(): any {
    if (!this._tripData.defaultTextProp) {
      this._tripData.defaultTextProp = { };
    }
    return this._tripData.defaultTextProp;
  }

  defaultEdgeProp(): TripEdgeProperties {
    if (!this._tripData.defaultEdgeProp) {
      this._tripData.defaultEdgeProp =
        SeqTrip.defaultTrip().defaultEdgeProp;
    }
    return this._tripData.defaultEdgeProp;
  }

  edgeProperty(edge: TripEdge, prop:string) {
    return edge.properties[prop] || this._tripData.defaultEdgeProp[prop];
  }

  nodeProperty(node: TripNode, prop:string) {
    return node.properties[prop] || this._tripData.defaultTextProp[prop];
  }

  reset() {
    this._tripData = SeqTrip.defaultTrip();
  }

  static defaultTrip() : TripSaved {
    const w = window as any;
    const prefs = globalMapParams().preferences || {};

    let pageSize : IPageSize = new PageSize('Full page', 128, 190);

    if (prefs.default_page_size && prefs.page_sizes) {
      for (let p of prefs.page_sizes) {
        if (p.name == prefs.default_page_size) {
          pageSize = p;
          break;
        }
      }
    }

    return {
      places: [],
      stages: [],
      additionalPlaces: [],
      location: { x: .5, y: .5, scale: 1/8 },
      defaultRadius: 8,
      width: pageSize.width_mm,
      height: pageSize.height_mm,
      pageSizeName: pageSize.name,
      borderColor: '#fefefe',
      landColor: '#cecece',
      seaColor: '#c4e3ff',
      showRivers: false,
      world: { },
      defaultTextProp: Object.assign({
          fontSize: 12,
          font: "'PT Sans', sans-serif",
          stroke: 'rgba(255,255,255,.8)',
          fill: '#000000'
        },
        prefs.default_node_prop),
      defaultEdgeProp: Object.assign({
          lineWidth: 2,
          lineColor: '#000000',
          geom: prefs.line_shape || 'straight'
        },
        prefs.default_edge_prop),
    };
  }


  setPlaces(places: TripNode[]) {
    // we preserve the same array, in order not to invalidate references
    this._tripData.places.splice.apply(
        this._tripData.places,
        ([0, this._tripData.places.length] as any[]).concat(places));
    this.updateStages();
    this.updateLandingsAndTakeoff();
  }

  public stagesAsDict() : { [id: string] : { [id: string] : TripEdge } } {
    const edgeStart : { [id: string] : { [id: string] : TripEdge } } = {};
    for (let s of this._tripData.stages) {
      if (!(s.from in edgeStart)) {
        edgeStart[s.from] = { };
      }
      edgeStart[s.from][s.to] = s;
    }
    return edgeStart;
  }

  public edgeFrom(name: string): TripEdge {
    for (let s of this._tripData.stages) {
      if (s.from == name) {
        return s;
      }
    }
  }

  private updateStages() {
    const places = this._tripData.places;
    const numStages = places.length - 1;
    const stages = this._tripData.stages;
    const newStages = [];
    const stageDict = this.stagesAsDict();

    for (let i = 1; i <= numStages; ++i) {
      const fromDbId = places[i - 1].dbid;
      const toDbId = places[i].dbid;

      if (stageDict[fromDbId] && stageDict[fromDbId][toDbId]) {
        const existing = stageDict[fromDbId][toDbId];
        existing.to = places[i].name;
        existing.fromIndex = i - 1;
        newStages.push(existing);
      } else {
        newStages.push(new TripEdge({
          from: places[i - 1].name,
          fromIndex: i - 1,
          to: places[i].name,
          fromDbId: fromDbId,
          controlPoints: TripGraph.generateControlPoints(
              (i >= 2 ? places[i - 2].coord : undefined),
              places[i - 1].coord,
              places[i].coord,
              ((i + 1) < places.length ? places[i + 1].coord : undefined)
              ),
          properties: places[i - 1].followingEdgeProp || { }
        }));
      }
    }
    // we preserve the same array, in order not to invalidate references
    this._tripData.stages.splice.apply(
      this._tripData.stages,
      [0, this._tripData.stages.length].concat(newStages));
  }

  private updateLandingsAndTakeoff() {
    const index : { [key: string]: number } = { };
    const additionalPlaces = this._tripData.additionalPlaces;

    for (let i = 0; i < additionalPlaces.length; ++i) {
      index[additionalPlaces[i].name] = i;
    }

    const placesToAdd : TripNode[] = [];
    const placesToRemove: { [key: string]: boolean } = {};
    const placesToKeep: { [key: string]: boolean } = {};

    const verbs = ['landing', 'takeoff'];

    for (let n of this._tripData.places) {
      for (let verb of verbs) {
        const name = n.dbid + '-' + verb;
        placesToKeep[name] = true;
        if (!n.properties.skip && n.properties[verb]) {
          // landing/takeoff wanted
          if (!(name in index)) {
            placesToAdd.push(SeqTrip.makePlaneIcon(verb, n));
          }
        } else {
          // landing/takeoff not wanted
          if (name in index) {
            placesToRemove[name] = true;
          }
        }
      }
    }

    const landingTakeoffRegex = /-(landing|takeoff)$/;
    const newPlaces =
      additionalPlaces.filter((n : TripNode) => {
          if (n.name in placesToRemove) {
            return false;
          }
          if (n.name in placesToKeep) {
            return true;
          }
          return n.name.match(landingTakeoffRegex) == null;
        })
      .concat(placesToAdd);

    this._tripData.additionalPlaces.splice.apply(
        this._tripData.additionalPlaces,
        ([0, this._tripData.additionalPlaces.length] as any[]).concat(newPlaces));
  }

  nodeRadius(node: TripNode, pixelRatio: number): number {
    let r = this._tripData.defaultRadius;
    if (node.properties && node.properties.radius) {
      r = node.properties.radius;
    }
    return r * pixelRatio;
  };

  makeGraph(tripLayer?: TripGraphLayer): TripGraph {
    const renameDict = {};
    const fuseNodePair = function(a, b) {
      const r : TripNode = TripNode.parse(a);
      r.name = a.name + ',' + b.name;
      r.allPlaces = a.allPlaces.concat([ b ]);

      const labels = a.label.split('\n');
      if (labels.indexOf(b.label) < 0) {
        labels.push(b.label);
      }
      r.label = labels.join('\n');
      renameDict[a.name] = renameDict[b.name] = r.name;
      return r;
    };

    const lookup = function(name) {
      let r = name;
      while (r in renameDict) {
        r = renameDict[r];
      }
      return r;
    };

    for (let n of this._tripData.places) {
      if (!n.name) {
        throw(new Error('nodes should have names and labels'));
      }
    }

    const keptNodes : TripNode[] = [];
    if (tripLayer) {
      const pinchZoom = tripLayer.renderer.pinchZoom;
      const pixelRatio = tripLayer.renderer.pixelRatio;

      for (let node of this._tripData.places) {
        node.viewerPos = pinchZoom.viewerPosFromWorldPos(node.coord);
      }

      for (let node of this._tripData.places) {
        if (!node.label || (node.properties && node.properties.skip)) {
          continue;
        }

        const r = this.nodeRadius(node, pixelRatio);
        let keep = true;

        for (let i = 0; i < keptNodes.length; ++i) {
          const n = keptNodes[i];
          const dist = Point.dist(n.viewerPos, node.viewerPos);
          if (dist < (this.nodeRadius(n, pixelRatio) + r)) {
            keptNodes[i] = fuseNodePair(n, node);
            keep = false;
            break;
          }
        }
        if (keep) {
          if (!node.properties) {
            node.properties = {};
          }
          const newNode = TripNode.parse(node);
          newNode.place = node;
          newNode.allPlaces = [ node ];
          keptNodes.push(newNode);
        }
      }
    } else {
      for (let node of this._tripData.places) {
        const newNode = TripNode.parse(node);
        newNode.place = node;
        newNode.allPlaces = [ node ];
        keptNodes.push(newNode);
      }
    }

    const places = this._tripData.places;
    const placeIndex : { [key: string]: number } = {};
    for (let i = 0; i < places.length; ++i) {
      const n = places[i];
      placeIndex[n.name] = i;
    }

    const result = new TripGraph(undefined, this._tripData.defaultEdgeProp);

    for (let n of keptNodes) {
      result.nodes[n.name] = n;
    }

    this.updateLandingsAndTakeoff();
    for (let n of this._tripData.additionalPlaces) {
      result.nodes[n.name] = n;
    }

    for (let edge of this._tripData.stages) {
      if (edge.properties.hidden || (places[edge.fromIndex] && places[edge.fromIndex].isSkipped())) {
        continue;
      }
      const nameFrom = lookup(edge.from);

      let to = edge.fromIndex + 1;
      if (Number.isNaN(to)) {
        continue;
      }
      while (to < places.length && places[to].isSkipped()) {
        ++to;
      }
      if (to >= places.length || places[to].isSkipped()) {
        continue;
      }
      const nameTo = lookup(places[to].name);

      if (nameFrom !== nameTo) {
        const newEdge = TripGraphLayer.shallowCopy(edge);
        newEdge.from = nameFrom;
        newEdge.to = nameTo;
        newEdge.originalEdge = edge;
        const route = newEdge.properties.route;
        if (route && (route.from != nameFrom || route.to != nameTo)) {
          delete newEdge.properties.route
        }
        result.edges.push(newEdge);
      }
    }
    return result;
  }

  save(): TripSaved { return this._tripData; }

  load(data: any) {
    if (typeof(data) == 'string') {
      data = JSON.parse(data);
    }

    if (!data || !data.places || !data.stages) {
      throw new Error('no data');
    }

    const newTripData = data as TripSaved;

    const parseNodeArray = (places: TripNode[]) => {
      for (let i = 0; i < places.length; ++i) {
        places[i] = TripNode.parse(places[i]);
      }
    };

    parseNodeArray(newTripData.places);
    const edges = newTripData.stages;
    for (let i = 0; i < edges.length; ++i) {
      edges[i] = new TripEdge(edges[i]);
    }

    parseNodeArray(newTripData.additionalPlaces);

    this._tripData = newTripData;
    this.defaultEdgeProp();
    this.setSize(data.pageSizeName, data.width, data.height);
  };

  seaColor() : string { return this._tripData.seaColor; }
  setSeaColor(c: string) {
    this._tripData.seaColor = c;
  }
  landColor() : string {
    return this._tripData.landColor;
  }
  setLandColor(c: string) { this._tripData.landColor = c; }
  borderColor() : string { return this._tripData.borderColor; }
  setBorderColor(c: string) { this._tripData.borderColor = c; }
  showRivers() : boolean { return this._tripData.showRivers; }
  toggleShowRiver() { this._tripData.showRivers = !this._tripData.showRivers; }

  language(): string { return this._tripData.language || 'en'; }
  setLanguage(lang: string) { this._tripData.language = lang; }

  placeByName(name: string): TripNode {
    for (let n of this._tripData.places) {
      if (name === n.name) {
        return n;
      }
    }
  }

  markFirstLocation() {
    // Remove firstVisited from all.
    for (let node of this._tripData.places) {
      if ('firstVisited' in node.properties) {
        delete node.properties.firstVisited;
      }
    }

    // put it back at the first visited place.
    for (let node of this._tripData.places) {
      if (!node.label || (node.properties && node.properties.skip)) {
        continue;
      }
      node.properties.firstVisited = true;
      return;
    }
  }

  clearUserPlacements() {
    for (let n of this._tripData.additionalPlaces
         .concat(this._tripData.places)) {
      if (n.properties) {
        if (n.properties.userPlacement) {
          delete n.properties.userPlacement;
        }
        if (n.properties.userPlacementPixelOffset) {
          delete n.properties.userPlacementPixelOffset;
        }
      }
    }
  }

  getMapStyle() {
    return (
      // For now, all maps follow the style in the theme.
      // Uncomment the following line to honour per-map map style.
      // this._tripData.mapStyle ||
      globalMapParams().preferences.map_style
      || 'streets');
  }

  filterAdditionalPlace(f: (node: TripNode) => boolean) {
    const filtered = this._tripData.additionalPlaces.filter(f);

    this._tripData.additionalPlaces.splice.apply(
        this._tripData.additionalPlaces,
        ([0, this._tripData.additionalPlaces.length] as any[]).concat(filtered));
  }

  setAdditionalPlace(node: TripNode) {
    const places = this._tripData.additionalPlaces;
    const existing = places.find((x) => x.name == node.name);
    if (existing) {
      places[places.indexOf(existing)] = node;
    } else {
      places.push(node);
    }
  }

  renameNodes(name: string, oldLabel: string, newLabel: string) {
    for (let n of this._tripData.places) {
      if (n.name.search(name) != -1) {
        n.label = n.label.replace(oldLabel, newLabel);
      }
    }
  }
}



