import { Component, Input, Output, DoCheck, KeyValueDiffers, EventEmitter } from '@angular/core';
import { ColorPickerService, Hsva, Rgba } from 'ngx-color-picker';

import { TripNodeProperties } from '../trip-map/trip-node';

import {IPoint, Point} from '../trip-map/point';

@Component({
  selector: 'text-prop',
  templateUrl: 'text-prop.component.html',
  styleUrls: ['./text-prop.component.css']
})
export class TextPropComponent {
  @Input() @Output() properties: TripNodeProperties;
  @Input() defaults?: TripNodeProperties;
  @Input() noReset?: boolean;
  @Output() onChange: EventEmitter<TripNodeProperties> = new EventEmitter();

  private fill: string;
  private stroke: string;
  private fontSize: number;

  private defaultsDiffer: any;
  private propertiesDiffer: any;

  constructor(private cpService: ColorPickerService,
              private differs: KeyValueDiffers) {
    this.defaultsDiffer = differs.find({}).create(null);
    this.propertiesDiffer = differs.find({}).create(null);
  }

  ngOnInit() { }

  changeEvent() {
    this.onChange.emit(this.properties);
  }

  ngDoCheck() {
    let changed =
      this.defaultsDiffer.diff(this.defaults)
      || this.propertiesDiffer.diff(this.properties);
    if (changed) {
      this.readProperties();
    }
    return changed;
  }

  readProperties() {
    for (let prop of ['fill', 'stroke', 'fontSize']) {
      if (this.properties && prop in this.properties) {
        this[prop] = this.properties[prop];
      } else if (this.defaults && prop in this.defaults) {
        this[prop] = this.defaults[prop];
      } else if (prop == 'stroke' || prop == 'fill') {
        this[prop] = '#000000';
      } else {
        this[prop] = undefined;
      }
    }
  }

  hasDefault() {
    for (let prop of ['fill', 'stroke', 'fontSize']) {
      if (prop in this.properties) {
        return false;
      }
    }
    return true;
  }

  setProperty(id: string) {
    this.properties[id] = this[id];
    this.changeEvent();
  }
}



