export interface IPageSize {
    name: string,
    width_mm: number,
    height_mm: number
}

export class PageSize {

  constructor(
    public name: string,
    public width_mm: number,
    public height_mm: number) { }

  static mmToPoint = 2.83465;

  static fromPt(name: string, w_pt: number, h_pt: number) : PageSize {
    return new PageSize(name, w_pt / PageSize.mmToPoint,
                        h_pt / PageSize.mmToPoint);
  }

  static fromIPageSize(x: IPageSize) {
    return new PageSize(x.name, x.width_mm, x.height_mm);
  }

  widthPt() { return Math.round(this.width_mm * PageSize.mmToPoint); }
  heightPt() { return Math.round(this.height_mm * PageSize.mmToPoint); }
  key(): string { return Math.round(this.width_mm) + 'x' + Math.round(this.height_mm); }
};





