import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpClientModule } from '@angular/common/http';

import {ColorPickerModule} from 'ngx-color-picker';

import { AppComponent } from './app.component';
import { TripMapComponent } from './trip-map/trip-map.component';
import { ViewSeqComponent } from './trip-map/view-seq.component';
import { TextPropComponent } from './text-prop/text-prop.component';
import { EdgePropComponent } from './trip-map/edge-prop/edge-prop.component';
import { MapView } from './trip-map/mapview.component';
import { MapView2 } from './trip-map/mapview2.component';

import { ButtonsModule } from 'ngx-bootstrap/buttons';
import { AccordionModule } from 'ngx-bootstrap/accordion';
import { TabsModule } from 'ngx-bootstrap/tabs';
import { BsDropdownModule } from 'ngx-bootstrap/dropdown';

@NgModule({
  declarations: [
    AppComponent,
    TripMapComponent,
    ViewSeqComponent,
    TextPropComponent,
    EdgePropComponent,
    MapView,
    MapView2,
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpClientModule,
    ColorPickerModule,
    AccordionModule.forRoot(),
    ButtonsModule.forRoot(),
    TabsModule.forRoot(),
    BsDropdownModule.forRoot()
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



