import { Point, IPoint } from './point';

export interface RouteData {
  mean: string;
  geom: IPoint[];
  midpoint: number;
  from: string;
  to: string;
};

export type LineShape = 'straight' | 'spline' | 'route';

export interface TripEdgeProperties {
  hidden?: boolean;
  lineColor?: string;
  lineWidth?: number;
  _lineWidth?: number; // used as backup when selecting an edge
  dashed?: number[];
  travelMean?: 'Unspecified'|'Bike'|'Boat'|'Bus'|'Car'|'Helicopter'|'Plane'|'Train'|'Walk';
  geom?: LineShape;
  route?: RouteData;
}

export class TripEdge {
  from: string;
  to: string;
  controlPoints: Point[];
  drawMiddlePoint?: boolean;
  originalEdge?: TripEdge;
  fromDbId: number;
  properties: TripEdgeProperties;
  fromIndex: number; // the index in seqTrip.places().

  constructor(obj?: any) {
    this.properties = { };

    if (obj) {
      if (obj.controlPoints) {
        this.controlPoints = [];
        for (let i = 0; i < obj.controlPoints.length; ++i) {
          this.controlPoints[i] = new Point(obj.controlPoints[i]);
        }
      }
      if (obj.originalEdge) {
        this.originalEdge = new TripEdge(obj.originalEdge);
      }
      for (let m of ['from', 'to', 'drawMiddlePoint', 'properties',
            'fromDbId' ]) {
        if (m in obj) {
          this[m] = obj[m];
        }
      }

      if (obj.properties) {
        this.properties = obj.properties;

        if (obj.properties.lineWidth) {
          this.properties.lineWidth = parseFloat(obj.properties.lineWidth);
        }

        if (obj.properties.dashed) {
          for (let i = 0; i < obj.properties.dashed.length; ++i) {
            this.properties.dashed[i] = parseInt(obj.properties.dashed[i]);
          }
        }
      }
    }
  }

  propagateToOriginal() {
    if (this.originalEdge) {
      for (let field of ['controlPoints', 'drawMiddlePoint' ]) {
        if (field in this) {
          this.originalEdge[field] = this[field];
        }
      }
    }
  }
}



