export class TripMapComponent implements OnInit {

  @ViewChild('mapview') mapview: MapView;

  public _canvasWidth: number;
  public _canvasHeight: number;

  private _renderer: CanvasTilesRenderer;
  private _worldLayer: WorldBackgroundLayer;
  private _tripLayer: TripGraphLayer;
  private _editor: TripGraphEditor;

  private _graph: TripGraph;
  private _selectedNode: TripNode;
  private _selectedCountry: string;
  private _selectedEdge: TripEdge;

  private _autoRefresh: number;
  private _saveTimeout?: any;

  public _visitedPlaceList: TripNode[];
  public _stages: TripEdge[];
  public _interleaved: EdgeOrNode[];
  public _seqTrip: SeqTrip;
  public _infoMessage?: string;
  public _errorMessage?: string;
  public _numLocations: number;
  public _autoLockMap: boolean;
  public _runAfterInit?: () => void;

  public language: string;
  private _needLabelPlacement?;

  private _kmlReader?: FileReader;

  // true when the map is loading.
  _loadingMap: boolean;
  _pageSizes : PageSize[];
  _pageSizeName : string;
  _pageSizeMap: { [key: string] : PageSize };
  _showSpinner: boolean;
  _saved: boolean;
  _scaleChangeTimeout;

  role: string;
  superadmin: boolean;

  constructor(public http: HttpClient) {
    const roleElement = document.querySelectorAll("meta[name^=role]")[0];
    this.role = (roleElement ? roleElement.getAttribute('content') : 'unknown');
    this.superadmin = (this.role == 'super-admin');
    this._canvasWidth = 512;
    this._canvasHeight = 512;
    this._selectedCountry = '';
    this._loadingMap = false;
    this._autoLockMap = true;

    const pageWidth = 128;
    const pageHeight = 190;
    if (globalMapParams().preferences.page_sizes) {
      this._pageSizes =
        globalMapParams().preferences.page_sizes.map(PageSize.fromIPageSize);
    } else {
      this._pageSizes = [
        new PageSize("Full page", pageWidth, pageHeight),
        new PageSize("Full page rotated", pageHeight, pageWidth),
        new PageSize("Square", pageWidth, pageWidth),
        new PageSize("Landscape", pageWidth, pageHeight / 2),
        /*
        new PageSize("A4 Portrait", 180, 267),
        new PageSize("A4 Landscape", 267, 180),
        new PageSize("A3 Portrait", 267, 390),
        new PageSize("A3 Landscape", 390, 267)
        */
      ];
    }
    this._pageSizeMap = {};
    for (let s of this._pageSizes) {
      this._pageSizeMap[s.key()] = s;
    }
    this._numLocations = 0;
    this._showSpinner = false;

    this._seqTrip = new SeqTrip();
    this._graph = new TripGraph(undefined, this._seqTrip.defaultEdgeProp());

    if (this._tripLayer) {
      this._tripLayer.defaultTextProp = this._seqTrip.defaultTextProp();
      this._tripLayer.defaultEdgeProp = this._seqTrip.defaultEdgeProp();
    }
    this._visitedPlaceList = this._seqTrip.places();
    this._stages = this._seqTrip.stages();
    this._interleaved = this.interleavedPlacesAndEvents();

    this.setPageSize(this._pageSizes[0].key());
    this.language = globalMapParams().language;
    this._seqTrip.setLanguage(globalMapParams().language);
  }

  fetchRawLocations() : Promise<TripLocation[]> {
    this._numLocations = 0;

    const params = globalMapParams();
    let promise;

    if (params.locations) {
      promise = Promise.resolve(
          // We clone the data to make sure the original remains untouch,
          // just in case fetchRawLocations() is called multiple times.
          params.locations.map((x) => Object.assign({}, x)));
    } else if (params.locations_url) {
      promise = new Promise((resolve, reject) => {
        this.http.get(params.locations_url, { responseType: 'json' })
          .subscribe(
          (data: any) => {
            if (Array.isArray(data)) {
              resolve(data as TripLocation[]);
            } else {
              reject('bad map data');
            }
          },
          (err: any) => { reject(err); });
      });
    } else {
      promise = Promise.resolve(undefined);
    }

    return promise.then((data) => {
      if (data) {
        this._numLocations = data.length;
      }
      return data;
    });
  }

  translateLocations(locationData : TripLocation[]) : Promise<TripLocation[]> {
    return new Promise((resolve, reject) => {
      for (let i in locationData) {
        // "translations" are sent with GeoTree points.
        // In that case, we should NOT query geoname translations.
        // We should honor "translations" as best as possible, or EN if
        // target language is not available.
        if (locationData[i].translations && locationData[i].translations[globalMapParams().language]) {
          locationData[i].label = locationData[i].translations[globalMapParams().language];
        }
        else if (locationData[i].translations && locationData[i].translations['en']) {
          locationData[i].label = locationData[i].translations['en'];
        }
      }
      const missing_translations =
        locationData
        .filter((loc : TripLocation) => loc.geoname_id && !loc.translations)
        .map((a : TripLocation) => a.geoname_id);
      if (missing_translations.length == 0) {
        resolve(locationData);
        return;
      }
      const url = globalMapParams().geoname_service_url
        + 'location?q=' + missing_translations.join(',')
        + '&l=' + globalMapParams().language;

      this.http.get(url, { responseType: 'json' })
        .subscribe((data: any[]) => {
         if (!data || !Array.isArray(data)) {
           resolve(locationData);
           return;
         }
         const dict = { };
         for (let d of data) {
           dict[d.id] = d;
         }
         for (let l of locationData) {
           if (l.geoname_id && (l.geoname_id in dict) && dict[l.geoname_id].name) {
             //console.log('Translated location: ' + l.label
             //            + ' -> ' + dict[l.geoname_id].name);
             l.label = dict[l.geoname_id].name;
           } else {
             //console.log('No geoname result for id: ' + l.geoname_id
             //            + ' (' + l.label + ')');
           }
         }
         return resolve(locationData);
       },
       (err: any) => { reject(err); });
    });
  }

  fetchLocations() : Promise<TripLocation[]> {
    return this.fetchRawLocations().then((data) => (data ? this.translateLocations(data) : undefined));
  }

  generateFromLocations(locations: TripLocation[]) {
    this._seqTrip.reset();
    this.updateWithLocations(locations, { resetLocation: true });
    if (this._tripLayer) {
      this._tripLayer.defaultTextProp = this._seqTrip.defaultTextProp();
      this._tripLayer.defaultEdgeProp = this._seqTrip.defaultEdgeProp();
    }
    this.saveMap();
  }

  async updateWithLocations(locations: TripLocation[],
                      options?: { resetLocation?: boolean; resetLabels?: boolean }) {
    options = options || { };
    const latLonToCoord = (lat: number, lon: number) => {
      const p = Utils.latLonToWorld([lon, lat]);
      return new Point(p);
    };
    const existingPlaces : { [key: number]: TripNode } = { };
    for (let p of this._seqTrip.places()) {
      if ('dbid' in p) {
        existingPlaces[p.dbid] = p;
      }
    }

    const places: TripNode[] = [];
    let numNewPlaces = 0;
    for (let l of locations) {
      const coord = latLonToCoord(l.latitude, l.longitude);
      const id = '' + l.id;
      if (id in existingPlaces) {
        const updatedPlace = existingPlaces[id];
        updatedPlace.coord = coord;
        if (!updatedPlace.label || options.resetLabels) {
          updatedPlace.label = l.label;
        }
        places.push(updatedPlace);
      } else {
        let newNode = TripNode.parse({
          dbid: id,
          name: id,
          label: l.label,
          coord: coord,
          properties: l.node_prop
        });
        if (l.following_edge_prop) {
          newNode.followingEdgeProp = l.following_edge_prop;
        }

        // A place might be visited multiple times, this appearing several
        // times in 'locations'. That's ok. However, we want all instances
        // to share the same properties object.
        for (let p of places) {
          if (p.dbid == newNode.dbid) {
            newNode.properties = p.properties;
            break;
          }
        }
        places.push(newNode);
        ++numNewPlaces;
      }
    }

    this._seqTrip.setPlaces(places);
    this._seqTrip.markFirstLocation();

    if (options.resetLocation) {
      this._seqTrip.setLocation(this._seqTrip.makeGraph().location(
              this._seqTrip.width_mm() / this._seqTrip.height_mm(), 1.2));
    }

    this._visitedPlaceList = this._seqTrip.places();
    this._stages = this._seqTrip.stages();
    this._interleaved = this.interleavedPlacesAndEvents();
    await this.pushSeqTripToCanvas(options.resetLocation);
    if (numNewPlaces > 0) {
      this.placeLabels(true);
      this._runAfterInit = () => { this.placeLabels(true); };
    } else {
      this._runAfterInit = () => { this.makeGraph(); };
    }
  }

  destination(edge: TripEdge): TripNode {
    if (this._tripLayer && edge.to in this._tripLayer.graph.nodes) {
      return this._tripLayer.graph.nodes[edge.to];
    } else {
      return this._seqTrip.placeByName(edge.to);
    }
  }
  origin(edge: TripEdge): TripNode {
    if (this._tripLayer && edge.from in this._tripLayer.graph.nodes) {
      return this._tripLayer.graph.nodes[edge.from];
    } else {
      return this._seqTrip.placeByName(edge.from);
    }
  }

  ngOnInit() {
    this.mapview.onMapReady = () => {
      if (this.isReady()) {
        console.log('Map is ready.');
      }
    };
    this.loadMap();
  }

  pushSeqTripToCanvas(fullReset?: boolean) : Promise<void> {
    for (let p of ['seaColor', 'borderColor', 'landColor', 'showRivers']) {
      //this._worldLayer.params[p] = this._seqTrip[p]();
    }
    //this._worldLayer.params.countryStyle = this._seqTrip.world();

    const pageSize = new PageSize(this._seqTrip.pageSizeName(),
                                  this._seqTrip.width_mm(),
                                  this._seqTrip.height_mm());
    if (!this._pageSizeMap[pageSize.key()]) {
      this._pageSizeMap[pageSize.key()] = pageSize;
      this._pageSizes.push(pageSize);
    }

    this.setPageSize(pageSize.key(), false);

    // Wait for setPageSize to propagate.
    return new Promise((resolve, reject) => setTimeout(resolve, 20))
      .then(() => {
        if (this._renderer) {
          this._renderer.setLocation(this._seqTrip.location());
          this._graph = this.mapview.setSeqTrip(this._seqTrip);
        }

        if (fullReset === true) {
          this.reset();
        } else if (this._renderer) {
          this.makeGraph();
        }
      });
  }

  ngAfterViewInit() {
  }

  // scale = 1 / ( 2^zoom )
  // 1 / scale = 2^zoom
  // log2(1/scale) = zoom
  scale() : number {
    const loc = this._seqTrip.location();
    return Math.log2(1 / loc.scale);
  }
  setScale(s: number) {
    const renderer = this._renderer;
    if (!renderer) {
      return;
    }
    const loc = renderer.getLocation();
    loc.scale =  1 / Math.pow(2, s);
    renderer.setLocation(loc);
    this._seqTrip.setLocation(this._renderer.getLocation());

    this.modified(ModificationEvent.Location);

    if (this._scaleChangeTimeout) {
      clearTimeout(this._scaleChangeTimeout);
    }
    this._scaleChangeTimeout = setTimeout(() => {
      this.placeLabels(true);
    }, 800);
  }

  prepareCanvas(cb?: () => void) {
    if (!this._seqTrip) {
      return;
    }
    this.mapview.prepareCanvas({
      disableMouseWheel: !this.hideUi(),
      trip: this._seqTrip,
      graph: this._graph,
      onLocationChange: (renderer: CanvasTilesRenderer) => {
          if (this._loadingMap || !this._seqTrip || !this._tripLayer || !this._editor) {
            return; // We let the map load before reacting to scroll/zoom.
          }
          const newLocation = renderer.getLocation();

          let needsPlacement = false;

          if (this._needLabelPlacement) {
            clearTimeout(this._needLabelPlacement);
            this._needLabelPlacement = undefined;
            needsPlacement = true;
          } else {
            // Zoom changes might fuse/split nodes, forcing a replace label.
            const fused = this._seqTrip.makeGraph(this._tripLayer);
            needsPlacement = !fused.compareNodes(this._tripLayer.graph);
          }

          if (needsPlacement) {
            // We postpone label placement once the user finished scrolling/moving
            // the map. We wait for 500ms without motion.
            this._needLabelPlacement = setTimeout(() => {
              console.log('Placing labels after motion.');
              this._seqTrip.setLocation(renderer.getLocation());
              const fused = this._seqTrip.makeGraph(this._tripLayer);
              this._graph = fused;
              this._tripLayer.graph = fused;
              this._editor.graph = fused;
              this.placeLabels(true);
              this._needLabelPlacement = undefined;
            }, 500);
          } else {
            // No fusion/split.
            this._seqTrip.setLocation(newLocation);
          }
          this.modified(ModificationEvent.Location);
      },
    alreadyCreated: (renderer: CanvasTilesRenderer) => {
      if (cb) {
        cb();
      }
    },
    created: (renderer: CanvasTilesRenderer, tripLayer: TripGraphLayer) => {
          this._renderer = renderer;
          this._tripLayer = tripLayer;

          // setTimeout is necessary to restart an angular check cycle.
          // In ngAfterViewInit, we're not supposed to change the state.
          setTimeout(async () => {
            this.clearSelection();
            this._editor = new TripGraphEditor(this._renderer, this._graph, tripLayer);
            this._renderer.pinchZoom.touchEventHandlers.push(this._editor);
            this._editor.onLabelMoved = (a) => { this.modified(ModificationEvent.LabelMoved); };
            this._editor.onBezierMoved = (a) => { this.modified(ModificationEvent.CurveMoved); };

            this._editor.onLabelSelect = (node: TripNode) => {
              this.selectNode(node);
            };
            this._editor.onBezierSelect = (bezier: {edge: TripEdge}) => {
              if (bezier) {
                this.selectEdge(bezier.edge);
              } else {
                this.clearSelection();
              }
            };

            await this.pushSeqTripToCanvas();
            this.mapview.resize();

            if (cb) { cb(); }
          }, 10);
        }
    });
  }

  mapIsLocked() : boolean {
    return this._renderer && this._renderer.pinchZoom && this._renderer.pinchZoom.locked;
  }

  lockMap(lock : boolean) {
    if (this._renderer && this._renderer.pinchZoom) {
      this._renderer.pinchZoom.locked = lock && this._autoLockMap;
    }
  }

  ngOnDestroy() {
    if (this._autoRefresh) {
      clearInterval(this._autoRefresh);
    }
  }

  clearSelection() {
    this._selectedCountry = undefined;
    this._selectedNode = undefined;
    this._selectedEdge = undefined;
  }

  clearAndSaveSelection() : Selection {
    const sel : Selection = {
      _selectedCountry: this._selectedCountry,
      _selectedNode: this._selectedNode,
      _selectedEdge: this._selectedEdge
    };
    this.clearSelection();
    return sel;
  }

  restoreSelection(sel : Selection) {
    this._selectedCountry = sel._selectedCountry;
    this._selectedNode = sel._selectedNode;
    this._selectedEdge = sel._selectedEdge;
  }

  selectCountry(country?: string) {
    this.clearSelection();
    this._selectedCountry = country;
    this.setCountryColor(country, this.countryColor(country));
  }

  setCountryColor(country: string, c: string) {
    if (!country) {
      return;
    }

    const wantsDefault = (c == 'default');

    let modified = false;
    let countryStyle; // = this._worldLayer.params.countryStyle[country];
    if (countryStyle) {
      if (!wantsDefault) {
        modified = countryStyle.color !== c;
        countryStyle.color = c;
      } else {
        modified = !!countryStyle.color;
        delete countryStyle.color;
        if (Object.keys(countryStyle).length == 0) {
          delete  this._worldLayer.params.countryStyle[country];
        }
      }
    } else {
      if (!wantsDefault) {
        modified = true;
        countryStyle = this._worldLayer.params.countryStyle[country] = {
          color: c
        };
      }
    }
    if (modified) {
      this.modified(ModificationEvent.MapColor);
    }
  }

  countryColor(country: string) {
    if (this._worldLayer
        && this._worldLayer.params.countryStyle[country]
        && this._worldLayer.params.countryStyle[country].color) {
      return this._worldLayer.params.countryStyle[country].color;
    }
    return this._seqTrip.landColor();
  }

  tunedCountries() : string[] {
    if (this._worldLayer) {
      return Object.keys(this._worldLayer.params.countryStyle);
    } else {
      return [];
    }
  }

  selectNode(node: TripNode) {
    this.clearSelection();

    if (node) {
      if (!node.properties) {
        node.properties = { };
      }
      if (node.name in this._graph.nodes) {
        this._selectedNode = this._graph.nodes[node.name];
      }

      if (node !== this._editor.selectedLabel) {
        const savedCallback = this._editor.onLabelSelect;
        const savedBezierCallback = this._editor.onBezierSelect;
        this._editor.onLabelSelect = () => {};
        this._editor.onBezierSelect = () => {};
        if (node.name in this._editor.graph.nodes) {
          this._editor.selectLabel(node);
        } else {
          this._editor.deselectLabel();
        }
        this._editor.onLabelSelect = savedCallback;
        this._editor.onBezierSelect = savedBezierCallback;
      }
    }
  }

  isNodeSelected(node: TripNode) {
    return (this._selectedNode && node
        && this._selectedNode.properties === node.properties)
        || (this._selectedEdge && this._selectedEdge.from.match(node.name));
  }

  selectEdge(edge: TripEdge) {
    this.clearSelection();
    this._selectedEdge = edge;
  }

  isEdgeSelected(edge: TripEdge) {
    return this._selectedEdge && this._selectedEdge.properties === edge.properties;
  }

  nodeNames(): string[] {
    return Object.keys(this._graph.nodes);
  }

  nodeArray(): TripNode[] {
    const r: TripNode[] = [];
    for (let k of Object.keys(this._graph.nodes)) {
      r.push(this._graph.nodes[k]);
    }
    return r;
  }

  isSkipped(node: TripNode): boolean {
    if (!node.properties || node.properties.skip === undefined) {
      return false;
    }
    return node.properties.skip;
  }

  toggleHideEdge(edge: TripEdge) {
    if (edge.properties.hidden) {
      delete edge.properties.hidden;

      for (let node of this._visitedPlaceList) {
        if ((node.name == edge.from || node.name == edge.to)
            && this.isSkipped(node)) {
          this.toggleSkipNode(node);
        }
      }
    } else {
      edge.properties.hidden = true;
    }
    this.modified(ModificationEvent.Edge);
  }

  toggleSkipNode(node: TripNode) {
    if (this.isSkipped(node)) {
      delete node.properties.skip;
    } else {
      if (!node.properties) {
        node.properties = { skip: true };
      } else {
        node.properties.skip = true;
      }
      /*
      for (let edge of this._stages) {
        if (edge.from == node.name || edge.to == node.name) {
          edge.properties.hidden = true;
        }
      }
     */
    }
    this.modified(ModificationEvent.Node);
  }

  toggleProperty(node: TripNode, property: string) {
    node.properties[property] = !node.properties[property];
    this.modified(ModificationEvent.Node);
  }

  setProperty(node: TripNode, property: string, value: any, ev: any) {
    if (value !== undefined) {
      node.properties[property] = value;
    } else {
      if (property in node.properties) {
        delete node.properties[property];
      }
    }
    this.modified(ModificationEvent.NodeStyle);
  }

  setNodeLabel(node: TripNode, newLabel: string) {
    const oldLabel = node.label;
    if (oldLabel === newLabel) {
      return;
    }
    this._seqTrip.renameNodes(node.name, oldLabel, newLabel);
    console.log('Renaming: "' + oldLabel + '" into "' + newLabel + '"');
    const graph = this._graph;
    for (let i in graph.nodes) {
      let n = graph.nodes[i];
      if (n.place === node || n.allPlaces.indexOf(node) >= 0) {
        n.label = n.label.replace(oldLabel, newLabel);
      }
    }
    node.label = newLabel;
    this.modified(ModificationEvent.Label);
  }

  removeEdge(edge: TripEdge) {
    const compareEdges =
      (e: TripEdge) => e.from !== edge.from || e.to !== edge.to;

    this._graph.edges = this._graph.edges.filter(compareEdges);
    this._editor.graph.edges = this._editor.graph.edges.filter(compareEdges);
  }

  uiSetFrame(width?: number, height?: number) {
    this.setFrame(width, height);
    this.modified(ModificationEvent.Frame);
    this.autoPlaceLabels();
  }

  setFrame(width?: number, height?: number) {
    if (!this._renderer) {
      return;
    }

    const w = this._canvasWidth;
    const h = this._canvasHeight;

    // Making a new graph makes sure to split all fused places first.
    const newLocation = this._seqTrip.makeGraph().location(w / h, 1.2);
    this._seqTrip.setLocation(newLocation);
    this._renderer.setLocation(newLocation);
  }

  setMapboxLocation(loc: Location) {
    this.mapview.setMapboxLocation(loc);
  }


  getLocation(): Location {
    return this._renderer.getLocation();
  }

  autoPlaceLabels() {
    this._seqTrip.clearUserPlacements();
    this.placeLabels(false);
  }

  placeLabels(preferLastPosition: boolean) {
    if (!this._tripLayer || !this._renderer || !this._seqTrip) {
      return;
    }
    this.runBlockingTask((done: () => void) => {
      this._tripLayer.placeLabels(this._renderer.canvas.getContext('2d'), preferLastPosition);
      this.modified(ModificationEvent.LabelMoved);
      this.mapview.refresh();
      done();
    });
  }

  runBlockingTask(task:(done: () => void) => void) {
    this._showSpinner = true;

    // make sure the interface updates
    setTimeout(() => {
      try {
        task(() => { this._showSpinner = false; });
      } catch (err) {
        // make sure to hide the spinner even if there is an exception
        this._showSpinner = false;
        console.log(err);
      }
    }, 30);
  }

  update() {
    this.placeLabels(true);
  }

  resetCurves() {
    const graph = this._graph;

    graph.createDefaultBezier();
    const fused = this._seqTrip.makeGraph(this._tripLayer);

    this._graph = fused;
    this._tripLayer.graph = fused;
    this._editor.graph = fused;

    this._tripLayer.placeLabels(this._renderer.canvas.getContext('2d'), true);
    this.mapview.refresh();
    this.modified(ModificationEvent.CurveMoved);
  }

  makeGraph() {
    this._graph =
      this._seqTrip.makeGraph(this._tripLayer);
    if (this._tripLayer) {
      this._tripLayer.graph = this._graph;
    } else {
      console.log('Warning: makeGraph called before this._tripLayer is ready.');
    }
    if (this._editor) {
      this._editor.graph = this._graph;
    }
    this._interleaved = this.interleavedPlacesAndEvents();
  }

  recreate() {
    if (this._visitedPlaceList.length) {
      this.makeGraph();
      this._seqTrip.markFirstLocation();
      this.placeLabels(true);
    }
  }

  reset() {
    if (this._visitedPlaceList.length) {
      this.makeGraph();
      this.setFrame();
      this.placeLabels(false);
    }
  }

  uiSetPageSize(pageSizeName: string, placeLabels?: boolean) {
    this.setPageSize(pageSizeName, placeLabels);
    this.modified(ModificationEvent.PageSize);
  }

  setPageSize(pageSizeName: string, placeLabels?: boolean) {
    const pageSize = this._pageSizeMap[pageSizeName];
    if (pageSize) {
      this._pageSizeName = pageSizeName;
      const width = pageSize.widthPt();
      const height = pageSize.heightPt();

      const factor = 1.1;
      this._canvasWidth = width * factor;
      this._canvasHeight = height * factor;
      this._seqTrip.setSize(pageSize.name, pageSize.width_mm, pageSize.height_mm);
      if (placeLabels !== false) {
        this.setFrame(this._canvasWidth, this._canvasHeight);
        setTimeout(() => { this.placeLabels(true); }, 20);
      }
    }
    if (this.mapview) {
      setTimeout(() => {
        this.mapview.resize();
      }, 0);
    }
  }

  setLayerColors() {
    this.mapview.setLayerColors(this._seqTrip);
  }

  setSeaColor(c: string) {
    if (c != this._seqTrip.seaColor()) {
      this._seqTrip.setSeaColor(c);
      //this._worldLayer.params.seaColor = c;
      this.setLayerColors();
      this.modified(ModificationEvent.MapColor);
    }
  }
  setBorderColor(c: string) {
    if (c != this._seqTrip.borderColor()) {
      this._seqTrip.setBorderColor(c);
      this.setLayerColors();
      //this._worldLayer.params.borderColor = c;
      this.modified(ModificationEvent.MapColor);
    }
  }
  setLandColor(c: string) {
    if (c != this._seqTrip.landColor()) {
      this._seqTrip.setLandColor(c);
      this.setLayerColors();
      //this._worldLayer.params.landColor = c;
      this.modified(ModificationEvent.MapColor);
    }
  }
  toggleShowRiver() {
    this._seqTrip.toggleShowRiver();
    this._worldLayer.params.showRivers = this._seqTrip.showRivers();
    this.modified(ModificationEvent.MapColor);
  }

  fetchMap() : Promise<any> {
    const params = globalMapParams();
    if (params.map_settings) {
      return Promise.resolve(params.map_settings);
    }

    return new Promise((resolve, reject) => {
      this.http.get(params.map_settings_url, { responseType: 'json' })
        .subscribe((data: any[]) => {
          resolve(data);
        },
        (err: any) => { reject(err); });
    });
  }

  /* The data flow is the following:
   *
   * existing map?
   *   yes => update with locations, done.
   *   no => #locations > 0 ?
   *     no => ask the user to populate the field
   *     yes => generate a new map from locations, save it.
   */
  loadMap() {
    this._loadingMap = true;
    this.fetchMap().then((data) => {
        try {
          this._seqTrip.load(data);
          this._seqTrip.setLanguage(globalMapParams().language);
          this._saved = true;
          this._visitedPlaceList = this._seqTrip.places();
          this._stages = this._seqTrip.stages();
          this._interleaved = this.interleavedPlacesAndEvents();

          return this.fetchLocations().then((locations) => {
            if (locations) {
              this.updateWithLocations(locations);
            }
          }).catch((err) => {
            this.handleError(err);
          });
        } catch(e) {
          // This error occurs when no map exists (seqTrip.load will throw).
          // This is totally Ok, we just have to make a new map.
          return this.fetchLocationsAndGenerateMap();
        }
      })
    .then(() => this.pushSeqTripToCanvas(false))
    .then(() => {
        this.prepareCanvas(() => {
            if (this._runAfterInit) {
              this._runAfterInit();
            }
            this._loadingMap = false;
        });
     })
    .catch((err) => {
      this.pushSeqTripToCanvas(false);
      this.prepareCanvas(() => { this._loadingMap = false; });
      this.handleError(err);
     });
  }

  isReady() : boolean {
    return (!this._loadingMap) && this.mapview.isReady();
  }

  fetchLocationsAndGenerateMap() {
    return this.fetchLocations()
      .then((locations) => {
        if (locations.length > 0) {
          this.generateFromLocations(locations);
        }
      })
  }

  saveMap() {
    const sel = this.clearAndSaveSelection();
    if (this._editor) {
      this._editor.deselectLabel();
    }
    // before we can save, we have to propagate edge edits in the graph to _seqTrip.
    // TODO: improve the design for that.
    if (this._graph) {
      for (let e of this._graph.edges) {
        e.propagateToOriginal();
      }
    }

    const url = globalMapParams().map_settings_url;

    if (!url) {
      this._saved = true;
    } else {
      $.ajax({
          type: 'PUT',
          url: url,
          dataType: 'text',
          contentType: "application/json",
          data: JSON.stringify({ _method: 'PUT', map_data: JSON.stringify(this._seqTrip.save()) }),
          success: () => { this._saved = true; this._errorMessage = undefined; },
          error: (jqXHR: any, textStatus: string, errorThrown: string ) => {
            this.handleError("Error: Can't save map. Please check your internet connection.");
          }
      });
    }

    this.restoreSelection(sel);
  }

  private handleError (error: any) {
    // In a real world app, we might use a remote logging infrastructure
    let errMsg: string;
    if (typeof(error) == 'object') {
      errMsg =  '' + ('message' in error ? error.message : error);
    } else if (typeof(error) == 'string') {
      errMsg = error;
    } else {
      errMsg = 'Undefined error';
    }
    console.log(error);
    this._infoMessage = undefined;
    this._errorMessage = errMsg;
  }

  nodePropertiesChanged() {
    this.modified(ModificationEvent.LabelProperties)
    this.modified(ModificationEvent.NodeStyle)
  }

  edgePropertiesChanged(edge?: TripEdge) {
    this.modified(ModificationEvent.EdgeProperties)
  }

  modified(what: ModificationEvent) {
    if (this._loadingMap) {
      return;
    }

    this._saved = false;

    if (what == ModificationEvent.Location) {
      this.mapview.updateCanvas();
    } else {
      this.mapview.refresh();
    }

    switch (what) {
      case ModificationEvent.UpdatedLocations:
      case ModificationEvent.LabelMoved:
      case ModificationEvent.CurveMoved:
      case ModificationEvent.Location:
        // no need to refresh: the canvas takes care of it.
        break;

      case ModificationEvent.NodeStyle:
      case ModificationEvent.LabelProperties:
      case ModificationEvent.EdgeProperties:
      case ModificationEvent.MapColor:
        this._seqTrip.markFirstLocation();
        this.mapview.refresh();
        break;

      case ModificationEvent.Label:
        this.placeLabels(true);
        break;

      case ModificationEvent.Node:
      case ModificationEvent.Edge:
      case ModificationEvent.Frame:
        // replace labels, but preserve map frame
        this.recreate();
        break;

      case ModificationEvent.PageSize:
        // set page size already resets everything, including map frame
        break;
    }

    if (this._saveTimeout) {
      clearTimeout(this._saveTimeout);
    }
    this._saveTimeout = setTimeout(() => {
        if (!this._loadingMap) { this.saveMap(); }
      }, 500);
  }

  attribution() {
    if (this.mapview) {
      return this.mapview.attribution().join('|');
    }
    return '';
  }

  interleavedPlacesAndEvents() {
    const result : EdgeOrNode[] = [];
    const edgeStart : { [id: string] : { [id: string] : TripEdge } } = this._seqTrip.stagesAsDict();
    const placesShown : { [id: string] : TripNode } = {};

    const places = this._visitedPlaceList;

    const addPlace = (p : TripNode) => {
      if (p.dbid in placesShown) {
        result.push({ node: placesShown[p.dbid] });
      } else {
        placesShown[p.dbid] = p;
        result.push({ node: p });
      }
    };

    const numStages = places.length - 1;
    for (let i = 0; i < places.length; ++i) {
      const p = places[i];
      let toDbId;

      if (i < numStages) {
         toDbId = places[i + 1].dbid;

        if (p.dbid === toDbId) {
          continue;
        }
      }

      addPlace(p);

      if (toDbId !== undefined) {
        if (p.dbid in edgeStart && toDbId in edgeStart[p.dbid]) {
          result[result.length - 1].edge = edgeStart[p.dbid][toDbId];
        }
      }
    }

    return result;
  }

  download(format, args?) {
    const randomString = (length) =>
      Math.round((Math.pow(36, length + 1) - Math.random() * Math.pow(36, length))).toString(36).slice(1);

    const key = randomString(16);
    const url = globalMapParams().map_print_service_url + 'mapgen/' + key;
    let pdfUrl = url + '/' + format;
    if (args) {
      pdfUrl += '?' +
        Object.keys(args)
        .map((key) => encodeURIComponent(key) + '=' + encodeURIComponent(args[key]))
        .join('&');
    }

    const data = Object.assign({}, globalMapParams());

    // For printing, no need to pass URL around, pass data.
    data.map_settings = this._seqTrip.save();
    if (data.map_settings_url) {
      delete data.map_settings_url;
    }
    if (data.locations_url) {
      delete data.locations_url;
    }
    data.hide_ui = true;  // No need to display interface when printing.

    this.http.post(url, JSON.stringify({ global_map_params: data }), {
      headers: new HttpHeaders({ 'content-type': 'application/json' })
      })
      .subscribe(
        response => {
          window.open(pdfUrl);
        },
        error => {
          console.log(error);
          this.handleError(error.message);
        }
      );
  }

  resetNodeToDefault(node : TripNode) {
    for (let field of Object.keys(node.properties)) {
      if (field != 'firstVisited') {
        delete node.properties[field];
      }
    }
    this.modified(ModificationEvent.Node);
  }

  resetLabels() {
    this.fetchLocations().then((locations) => {
        this.updateWithLocations(locations, { resetLabels: true });
        this.modified(ModificationEvent.Label);
      });
  }

  @HostListener('window:beforeunload', ['$event'])
  doSomething($event) {
    if(!this._loadingMap && this._numLocations > 0 && !this._saved) {
      $event.returnValue='Your data will be lost!';
    }
  }

  hideUi() {
    return globalMapParams().hide_ui || false;
  }

  allowDrop(e) {
    e.stopPropagation();
    e.preventDefault();
    e.dataTransfer.dropEffect = 'copy';
  }

  drag(ev) {
    ev.preventDefault();
  }

  readKmlFile(data: string) {
    const parser = new DOMParser();
    const kml = parser.parseFromString(data, "text/xml");
    const marks = kml.getElementsByTagName("Placemark");


    for (let i = 0; i < marks.length; ++i) {
      const placemark = marks[i];
      const name = placemark.getElementsByTagName("name")[0].textContent;
      const coords = placemark.getElementsByTagName("coordinates")[0]
        .textContent.split(",").map(parseFloat) ;

      let n = parseInt(name);
      if (isNaN(n)) {
        n = i + 1;
      }

      this._seqTrip.setAdditionalPlace(TripNode.parse({
        coord: new Point(Utils.latLonToWorld(coords)),
        name: 'kml-' + n,
        label: '' + n,
        properties: {
          point: false,
          blackdot: true,
          numberedName: name.replace(/^[0-9]*\. */, ''),
        },
      }));
    }
  }

  readJsonFile(data: string) {
    try {
      const geojson = JSON.parse(data);

      for (let feature of geojson) {
        if (feature.type != 'Feature'
            || feature.geometry.type != 'Point'
            || !feature.properties) {
          continue;
        }

        const n = feature.properties.index;

        this._seqTrip.setAdditionalPlace(TripNode.parse({
          coord: new Point(Utils.latLonToWorld(feature.geometry.coordinates)),
          name: 'kml-' + n,
          label: '' + n,
          properties: {
            point: false,
            blackdot: true,
            numberedName: feature.properties.name,
            articleId: feature.properties.articleID,
          },
        }));
      }
    } catch(err) {
      console.log(err);
    }
  }

  drop(ev) {
    ev.stopPropagation();
    ev.preventDefault();

    if (!this._kmlReader) {
      this._kmlReader = new FileReader();
      this._kmlReader.onload = (event: Event) => {
          this._seqTrip.filterAdditionalPlace((node: TripNode) => !node.name.match(/^kml-/));
          const data = this._kmlReader.result;
          if (data[0] == '[') {
            this.readJsonFile(data);
          } else {
            this.readKmlFile(data);
          }

          this.makeGraph();
          this.placeLabels(false);
          this.modified(ModificationEvent.Label);
        };
    }
    if (ev.dataTransfer && ev.dataTransfer.files.length > 0) {
      this._kmlReader.readAsText(ev.dataTransfer.files[0]);
    }
  }

  hasMark(node: TripNode): boolean {
    const prop = node.properties;
    if (!prop) {
      return false;
    }
    if (node.properties.markStyle) {
      return node.properties.markStyle == 'first';
    }
    return !!node.properties.firstVisited;
  }
}



