// This file specifies all parameters that the map editor can take as input.
// The rails equivalent can be found in app/models/map_params.rb
//
// To access the parameters, call globalMapParams() which returns a MapParams object.
//
// To send parameters to the map editor, fill a window.global_map_params object with
// MapParams data.

import { TripSaved } from './trip-saved';
import { IPageSize } from './page-size';
import { TripEdgeProperties, LineShape } from './trip-edge';
import { TripNodeProperties } from './trip-node';

export interface TripLocation {
  id: number,
  label: string,
  latitude: number,
  longitude: number
  geoname_id?: number;
  translations?: { [id: string]: string };

  // Those will be used on map creation, but changes might be ignored on map update.
  node_prop?: TripNodeProperties;
  following_edge_prop?: TripEdgeProperties;
}

export interface MapPreferences {
  // redundant with default_edge_prop.geom. If both are specified,
  // default_edge_prop.geom has priority.
  line_shape?: LineShape;

  map_style?: 'topo' | 'streets' | 'positron' | string;
  page_sizes?: IPageSize[];

  // the name of the page size to use when creating a new map, as defined in
  // IPageSize. Must reference a format in page_sizes.
  // if default_page_size is not defined or page_sizes does not contained the
  // specified page size, 'Full page' 128x190 will be used.
  default_page_size?: string;

  // Those defaults are copied on map creation. After, the user is free to change
  // them, and it is stored in MapParams.TripSaved.
  default_edge_prop?: TripEdgeProperties;
  default_node_prop?: TripNodeProperties;
}

export interface MapParams {
  locations?: TripLocation[];
  locations_url?: string;

  // Contains a json-encoded string or an object that can be loaded
  // by SeqTrip.load(). This data is opaque to Rails code.
  map_settings?: string | TripSaved;

  // The URL to GET or POST TripSaved data
  map_settings_url?: string;

  // The map preferences stored in the theme
  preferences?: MapPreferences;

  // The language that map labels have to be rendered to.
  language?: string;

  // Both of these urls are expected to have a trailing /:
  // For example:
  // "https://destinationbook-cssservice.herokuapp.com/"
  geoname_service_url: string;
  map_print_service_url: string;

  hide_ui?: boolean;
}

// Obsolete. This is for backward compatibility only.
declare var thisTripApi: {
  mapUrl: string;
  locationsUrl: string;
  tripLanguage: string;
  lineShape?: LineShape;
};

declare var mapJson: any;

// "entry point": this is the way to get access to the actual parameters.
export function globalMapParams(): MapParams {
  let win = window as { global_map_params?: MapParams };
  let map_params = win.global_map_params;

  // thisTripApi should not be used anymore. This is kept for backward
  // compatibility, for the transition period.
  if (!map_params && thisTripApi) {
    const cssservice = ((window as any).cssserviceUrl
                        || "https://destinationbook-cssservice.herokuapp.com/");
    win.global_map_params = {
      locations_url: thisTripApi.locationsUrl,
      map_settings_url: thisTripApi.mapUrl,
      preferences: { line_shape: thisTripApi.lineShape },
      language: thisTripApi.tripLanguage,
      geoname_service_url: cssservice,
      map_print_service_url: cssservice
    };
    map_params =  win.global_map_params;
  }

  // Compatibility with sass_compilator/map.html
  if (!map_params && typeof(mapJson) != 'undefined') {
    if (typeof(mapJson) == 'object' && mapJson.global_map_params) {
      // the "old" print service passes arguments in mapJson, not global_map_params.
      win.global_map_params = mapJson.global_map_params;
    } else {
      win.global_map_params = {
        map_settings: mapJson,
        language: mapJson.tripLanguage,
        geoname_service_url: '',
        map_print_service_url: '',
        hide_ui: true
      };
    }
    map_params = win.global_map_params;
  }

  if (!map_params) {
    throw new Error("Can't find map parameters");
  }

  // Make sure preferences exists, even if it is empty.
  if (!map_params.preferences) {
    map_params.preferences = {};
  }

  return map_params;
}



