import { AfterViewInit, Input, Component, ViewChild, ElementRef, HostListener } from '@angular/core';

import { CanvasTilesRenderer } from './tiles';
import { GetSeqTrip } from './some_map';
import { PageSize } from './page-size';
import { SeqTrip } from './seq-trip';
import { TripGraphLayer } from './trip-graph-layer';

declare var tripLanguage;

@Component({
  selector: 'view-seq',
  templateUrl: 'view-seq.component.html',
})
export class ViewSeqComponent implements AfterViewInit {
  @ViewChild('mapview') mapview;

  public width = 1024;
  public height = 1024;
  private trip: SeqTrip;
  public language: string;

  constructor() {
    this.trip = GetSeqTrip();
    this.width = this.trip.width_mm() * PageSize.mmToPoint;
    this.height = this.trip.height_mm() * PageSize.mmToPoint;
    this.language = (window as any).tripLanguage || this.trip.language();
  }
  ngAfterViewInit() {
    this.mapview.prepareCanvas({
      trip: this.trip
    });
  }
}



