import {Point, IPoint } from './point';
import { BoundingBox } from './bounding-box';
import { PinchZoom } from './tiles';
import { TripEdgeProperties } from './trip-edge';

export interface TripNodeProperties {
  point?: boolean;
  leaderLine?: string;
  dashed?: number[];
  leaderLineWidth?: number;
  maxLeaderLineLength?: number;
  labelBbox?: BoundingBox;
  radius?: number;
  leaderLineAnchor?: Point;
  leaderLineAnchorPixelOffset?: Point;
  labelColor?: string;

  // labelCoord is expressed in map coordinates.
  // It is obsolete.
  // labelCoord = unproject(project(coord) + labelPixelOffset)
  labelCoord?: Point;
  userPlacement?: Point;  // same thing, osbolete

  // labelPixelOffset = project(labelCoord) - project(coord)
  labelPixelOffset?: Point;
  userPlacementPixelOffset?: Point;

  frame?: string;
  leaderLineColor?: string;
  fontSize?: number;
  font?: string;
  stroke?: string;
  haloWidth?: number;
  fill?: string;
  textOffset?: number;
  textPlacement?: string;
  skip?: boolean;
  landing?: boolean;
  takeoff?: boolean;
  markStyle?: string;
  firstVisited?: boolean;
  blackdot?: boolean;  // show a black dot under label (for KML import)

  numberedName?: string;
  articleId?: number | string;
}

export interface ITripNode {
  coord: IPoint | number[];
  dbid?: number;
  name?: string;
  label?: string;
  labelIcon?: {
    url: string;
    width?: number;
    height?: number;
    autorotate?: number;
    angle?: number;
    ratioX?: number;
    ratioY?: number;
  };
  properties?: TripNodeProperties;

  viewerPos?: Point;
  place?: TripNode;
}

export class TripNode implements ITripNode {
  coord: Point;
  dbid?: number;
  name?: string;
  label?: string;
  labelIcon?: {
    url: string;
    width?: number;
    height?: number;
    autorotate?: number;
    angle?: number;
    ratioX?: number;
    ratioY?: number;
  };
  properties?: TripNodeProperties;

  viewerPos?: Point;
  place?: TripNode;
  allPlaces: TripNode[];

  followingEdgeProp?: TripEdgeProperties;

  constructor() {
    this.allPlaces = [];
  };

  static parse(obj: any): TripNode {
    const r = new TripNode();
    r.coord = new Point(obj.coord);
    if (obj.place) {
      obj.place = TripNode.parse(obj.place);
    }
    if (obj.viewerPos) {
      r.viewerPos = new Point(obj.viewerPos);
    }
    for (let m of ['dbid', 'name', 'label', 'labelIcon', 'properties']) {
      if (obj[m]) {
        r[m] = obj[m];
      }
    }
    if (!r.name) {
      if (r.dbid !== undefined) {
        r.name = '' + r.dbid;
      } else if (r.label) {
        r.name = r.label;
      }
    }

    r.properties = obj.properties || {};

    if (r.properties.labelBbox) {
      r.properties.labelBbox = new BoundingBox(r.properties.labelBbox);
    }
    if (r.properties.leaderLineAnchor) {
      r.properties.leaderLineAnchor = new Point(r.properties.leaderLineAnchor);
    }
    if (r.properties.labelCoord) {
      r.properties.labelCoord = new Point(r.properties.labelCoord);
    }
    if (r.properties.labelPixelOffset) {
      r.properties.labelPixelOffset = new Point(r.properties.labelPixelOffset);
    }

    return r;
  }

  isSkipped():boolean {
    if (this.label
        && (!this.properties || this.properties.skip === undefined)) {
      return false;
    }
    return this.properties.skip;
  }

  labelPixelPos(pinchZoom: PinchZoom, pixelRatio: number): Point {
    let pos: Point;
    if (this.properties && this.properties.labelPixelOffset) {
      // new style
      pos = Point.plus(
          pinchZoom.viewerPosFromWorldPos(this.coord),
          Point.times(pixelRatio, this.properties.labelPixelOffset));
    } else {
      // old style labelCoord, or coord directly
      pos = pinchZoom.viewerPosFromWorldPos(
        (this.properties && this.properties.labelCoord ?
         this.properties.labelCoord : this.coord));
    }
    return pos;
  }

  leaderLineAnchorPos(pinchZoom: PinchZoom, pixelRatio: number): Point | undefined {
    if (!this.properties
        || (!this.properties.leaderLineAnchor &&
            !this.properties.leaderLineAnchorPixelOffset)) {
      return;
    }
    if (this.properties.leaderLineAnchorPixelOffset) { 
      return Point.plus(
          pinchZoom.viewerPosFromWorldPos(this.coord),
          Point.times(pixelRatio, this.properties.leaderLineAnchorPixelOffset));
    }
    return pinchZoom.viewerPosFromWorldPos(this.properties.leaderLineAnchor);
  }

  convertToPixelOffset(pinchZoom: PinchZoom, pixelRatio: number) {
    if (this.properties.labelCoord) {
      if (!this.properties.labelPixelOffset) {
        // labelPixelOffset = project(labelCoord) - project(coord)
        this.properties.labelPixelOffset = Point.minus(
          pinchZoom.viewerPosFromWorldPos(this.properties.labelCoord),
          pinchZoom.viewerPosFromWorldPos(this.coord)).mul(1 / pixelRatio);
      }
      delete this.properties.labelCoord;
    }

    if (this.properties.leaderLineAnchor) {
      if (!this.properties.leaderLineAnchorPixelOffset) {
        this.properties.leaderLineAnchorPixelOffset =
          Point.minus(
            pinchZoom.viewerPosFromWorldPos(this.properties.leaderLineAnchor),
            pinchZoom.viewerPosFromWorldPos(this.coord)).mul(1 / pixelRatio);
      }
      delete this.properties.leaderLineAnchor;
    }
  }
}



