import { BoundingBox } from './bounding-box';
import { IPoint, Point } from './point';
import { TripEdge } from './trip-edge';
import { TripGraph } from './trip-graph';
import { TripGraphLayer } from './trip-graph-layer';
import { TripNode, TripNodeProperties } from './trip-node';
import { T, Location, CanvasTilesRenderer, CanvasTilesRendererParams, PinchZoom } from './tiles';

interface BezierEdge {
  edge: TripEdge;
}

declare var d3: any;
declare var Utils: any;

export class TripGraphEditor {
  graph: TripGraph;
  selectedLabel: TripNode;
  onLabelSelect: (node: TripNode) => void;
  renderer: CanvasTilesRenderer;
  tripLayer: TripGraphLayer;

  selectedBezier: BezierEdge;
  onBezierSelect?: (bezier: BezierEdge) => void;
  onBezierMoved?: (bezier: BezierEdge) => void;
  onLabelMoved?: (node: TripNode) => void;

  private startWorldPos: Point;
  private startViewerPos: Point;
  private applyDelta: (delta: IPoint, dviewer: IPoint) => void;

  constructor(renderer: CanvasTilesRenderer, graph: TripGraph, tripLayer: TripGraphLayer) {
    this.graph = graph;
    this.renderer = renderer;
    this.tripLayer = tripLayer;
  }


  acceptTouchEvent(viewer: IPoint, world: IPoint, type: string) {
    if (type === 'wheel') {
      return false;
    }

    this.startWorldPos = new Point(world);
    this.startViewerPos = new Point(viewer);
    const clickedLabel = this.tripLayer.findLabelAtWorldPos(this.startWorldPos);
    if (clickedLabel) {
      this.selectLabel(clickedLabel);
      const properties = clickedLabel.properties;
      this.applyDelta = function(delta: IPoint, dviewer: IPoint) {
        if (properties.labelCoord) {
          properties.labelCoord.x += delta.x;
          properties.labelCoord.y += delta.y;
          properties.userPlacement = new Point(properties.labelCoord);
        }
        if (properties.labelPixelOffset) {
          properties.labelPixelOffset.add(
              Point.times(1/this.renderer.pixelRatio, dviewer));
          properties.userPlacementPixelOffset = new Point(properties.labelPixelOffset);
        }
        properties.labelBbox.min.x += delta.x;
        properties.labelBbox.min.y += delta.y;
        properties.labelBbox.max.x += delta.x;
        properties.labelBbox.max.y += delta.y;
        TripGraph.placeLeaderLine(clickedLabel, this.renderer.pinchZoom,
                                  this.renderer.pixelRatio);
        if (this.onLabelMoved) {
          this.onLabelMoved(clickedLabel);
        }
      };
      return true;
    } else {
      this.deselectLabel();

      const p = this.closestBezier(world);

      const threshold = this.renderer.pinchZoom.worldDistanceFromViewerDistance(
          10 * this.renderer.pixelRatio);

      if (p && p.d < threshold) {
        this.selectBezier(p);

        if (!p.bezier) {
          return true;
        }

        const me = this;
        const pointToMove = function(ptOrArray?: Point | Point[]) {
          me.applyDelta = function(delta: IPoint, dviewer: IPoint) {
            if (!ptOrArray) { return; }
            const a: Point[] =
              (Array.isArray(ptOrArray) ? ptOrArray : [ptOrArray]);
            for (let point of a) {
              point.x += delta.x;
              point.y += delta.y;
            }
            if (this.onBezierMoved) {
              this.onBezierMoved(p);
            }
          };
        };

        const movePointWithOpposite = function(a, pivot, b) {
          me.applyDelta = function(delta, dviewer) {
            a.x += delta.x;
            a.y += delta.y;
            pivot.x += delta.x / 2;
            pivot.y += delta.y / 2;
          };
        };

        if (p.edge.controlPoints.length === 2) {
          pointToMove(p.edge.controlPoints[p.t < .5 ? 0 : 1]);
        } else if (p.edge.controlPoints.length === 5) {
          if (p.bezier.points[1].x === p.edge.controlPoints[0].x
              && p.bezier.points[1].y === p.edge.controlPoints[0].y) {
            if (p.t < .5) {
              pointToMove(p.edge.controlPoints[0]);
            } else if (p.t < .9) {
              movePointWithOpposite(p.edge.controlPoints[1],
                                    p.edge.controlPoints[2],
                                    p.edge.controlPoints[3]);
            } else {
              pointToMove([p.edge.controlPoints[1],
                          p.edge.controlPoints[2],
                          p.edge.controlPoints[3]]);
            }
          } else {
            if (p.t < .1) {
              pointToMove([p.edge.controlPoints[1],
                          p.edge.controlPoints[2],
                          p.edge.controlPoints[3]]);
            } else if (p.t < .5) {
              movePointWithOpposite(p.edge.controlPoints[3],
                                    p.edge.controlPoints[2],
                                    p.edge.controlPoints[1]);
            } else {
              pointToMove(p.edge.controlPoints[4]);
            }
          }
        } else {
          pointToMove(Point.nearest(world, p.edge.controlPoints));
        }
        return true;
      } else {
        this.selectBezier();
      }
    }

    this.applyDelta = undefined;
    return false;
  };

  selectLabel(label?: TripNode) {
    if (this.selectedLabel) {
      delete this.selectedLabel.properties.frame;
    }
    this.selectedLabel = undefined;
    this.selectBezier();
    this.selectedLabel = label;
    if (this.onLabelSelect) {
      this.onLabelSelect(label);
    }
    if (label) {
      label.properties = label.properties || {};
      label.properties.frame = '#000000';
    }
    this.renderer.refreshIfNotMoving();
  };

  deselectLabel() { return this.selectLabel(); };

  handleMouseDown(event: any) {
    event.preventDefault();
  }

  handleMouseMove(event: any) {
    event.preventDefault();
    const viewerPos = Utils.eventPosInElementCoordinates(
        event, event.target || event.srcElement);
    this.handleMotion(viewerPos);
  }

  handleMotion(viewerPos: Point) {
    if (!this.applyDelta) {
      return;
    }

    const worldPos = this.renderer.pinchZoom.worldPosFromViewerPos(
        viewerPos.x, viewerPos.y);

    const deltaWorld = Point.minus(worldPos, this.startWorldPos);
    this.startWorldPos = worldPos;

    const deltaViewer = Point.minus(viewerPos, this.startViewerPos);
    this.startViewerPos = new Point(viewerPos);

    this.applyDelta(deltaWorld, deltaViewer);
    this.renderer.refreshIfNotMoving();
  }

  handleMouseUp(event: any) {
    event.preventDefault();
    this.applyDelta = undefined;
  }

  handleMouseWheel(event: any) {
    event.preventDefault();
  }

  handleStart(event: any) {
    event.preventDefault();
  }

  handleEnd(event: any) {
    event.preventDefault();
    this.applyDelta = undefined;
  }

  handleMove(event: any) {
    event.preventDefault();
    if (event.touches.length !== 1) {
      this.applyDelta = undefined;
      return;
    }

    const viewerPos = Utils.eventPosInElementCoordinates(
        event.touches[0], event.target || event.srcElement);
    this.handleMotion(viewerPos);
  }

  edgeProperty(edge: TripEdge, prop: string) {
    return this.graph.edgeProperty(edge, prop);
  }

  closestBezier(p) {
    let nearest;
    for (let e of this.graph.edges) {
      const route = this.graph.getEdgeRoute(e);
      if (route) {
        for (let routePoint of route.geom) {
          const dist = Point.dist(p, routePoint);
          if (!nearest || dist < nearest.d) {
            nearest = {
              d: dist,
              edge: e,
              bezier: undefined
            };
          }
        }
      } else {
        const curves = this.graph.bezier(e);
        for (let b of curves) {
          const candidate = b.project(p);
          if (!nearest || candidate.d < nearest.d) {
            nearest = candidate;
            nearest.edge = e;
            nearest.bezier = b;
          }
        }
      }
    }
    return nearest;
  }

  selectBezier(b?: BezierEdge) {
    if (this.selectedBezier) {
      const edge = this.selectedBezier.edge;
      delete edge.drawMiddlePoint;
      if (edge.properties._lineWidth !== undefined) {
        edge.properties.lineWidth = edge.properties._lineWidth;
        delete edge.properties._lineWidth;
      } else {
        delete edge.properties.lineWidth;
      }
    }
    this.selectedBezier = b;

    if (b) {
      const edge = b.edge;
      edge.drawMiddlePoint = true;
      if (edge.properties.lineWidth !== undefined) {
        edge.properties._lineWidth = edge.properties.lineWidth;
      }
      edge.properties.lineWidth = 5;
    }
    if (this.selectedLabel) {
      this.deselectLabel();
    }

    if (this.onBezierSelect) {
      this.onBezierSelect(this.selectedBezier);
    }
    this.renderer.refreshIfNotMoving();
  }

  /*
  splitCurve() {
    if (!this.selectedBezier) {
      return;
    }

    const edge = this.selectedBezier.edge;

    this.graph.splitEdge(edge);
  }
  */
}



