import { Component, Input, Output, OnInit, EventEmitter, DoCheck, KeyValueDiffers } from '@angular/core';

import { TripEdgeProperties } from '../trip-edge';

class LineStyle {
  name: string;
  style: number[];

  key() : string { return name; }

  constructor(n: string, s: number[]) {
    this.name = n.replace(/-/g, '\u2501').replace(/ /g, ' \u00A0');
    this.style = s;
  }
}

@Component({
  selector: 'edge-prop',
  templateUrl: 'edge-prop.component.html',
  styleUrls: [ 'edge-prop.component.css' ],
})
export class EdgePropComponent implements OnInit {
  @Input() @Output() properties?: TripEdgeProperties;
  @Input() defaults?: TripEdgeProperties;
  @Input() noReset?: boolean;
  @Output() onChange: EventEmitter<TripEdgeProperties> = new EventEmitter();

  private lineColor: string;
  private lineWidth: number;
  private travelMean: string;

  private defaultsDiffer: any;
  private propertiesDiffer: any;
  private allLineStyles: LineStyle[];
  private lineStyle: string;
  private allTravelMeans: string[];
  private geom: string;

  constructor(private differs: KeyValueDiffers) {
    this.defaultsDiffer = differs.find({}).create(null);
    this.propertiesDiffer = differs.find({}).create(null);

    this.allLineStyles = [
      new LineStyle('------', []),
      new LineStyle('- - - ', [4, 4]),
      new LineStyle('--  --', [8, 8])
    ];

    this.allTravelMeans = [
      'Unspecified',
      'Bike',
      'Boat',
      'Bus',
      'Car',
      'Helicopter',
      'Plane',
      'Train',
      'Walk'
    ];
  }

  changeEvent() {
    this.setProp('lineWidth');
    this.onChange.emit(this.properties);
  }

  ngOnInit() {
    this.readProperties();
  }

  ngDoCheck() {
    let changed =
      this.defaultsDiffer.diff(this.defaults)
      || this.propertiesDiffer.diff(this.properties);
    if (changed) {
      this.readProperties();
    }
    return changed;
  }

  readProperties() {
    const defaults = {
      lineColor: '#000000',
      lineWidth: 2,
      travelMean: 'Unspecified',
      geom: 'straight'
    };
    for (let field of Object.keys(defaults)) {
      if (this.properties && field in this.properties) {
        this[field] = this.properties[field];
      } else if (this.defaults
                 && field in this.defaults) {
        this[field] = this.defaults[field];
      } else {
        this[field] = defaults[field];
      }
    }

    let dashed;
    if (this.properties && this.properties.dashed) {
      dashed = this.properties.dashed;
    } else if (this.defaults && this.defaults.dashed) {
      dashed = this.defaults.dashed;
    }
    if (dashed) {
      for (let s of this.allLineStyles) {
        if (this.sameStyles(s.style, dashed)) {
          this.lineStyle = s.name;
        }
      }
    } else {
      this.lineStyle = this.allLineStyles[0].name;
    }
  }

  sameStyles(a?: number[], b?: number[]) : boolean {
    if (!a || !b) {
      return false;
    }
    if (a === b) {
      return true;
    }
    if (a.length != b.length) {
      return false;
    }
    for (let i = 0; i < a.length; ++i) {
      if (a[i] != b[i]) {
        return false;
      }
    }
    return true;
  }

  setProp(field: string) {
    if (this.properties) {
      if (this.defaults
          && this[field] === this.defaults[field]) {
        if (this.properties[field]) {
          delete this.properties[field];
        }
      } else {
        this.properties[field] = this[field];
      }
    }
  }

  colorChanged() {
    this.setProp('lineColor');
    this.changeEvent();
  }
  
  resetToDefault() {
    for (let field of Object.keys(this.properties)) {
      delete this.properties[field];
    }
    this.readProperties();
    this.changeEvent();
  }

  hasDefault() {
    const fields = [ 'lineColor', 'lineWidth', 'travelMean', 'dashed' ];
    for (let f of fields) {
      if (f in this.properties) {
        return false;
      }
    }
    return true;
  }

  setLineStyle(style: string) {
    for (let s of this.allLineStyles) {
      if (s.name === style) {
        this.properties.dashed = s.style;
      }
    }
    this.changeEvent();
  }

  canDrive() : boolean {
    return ['Car', 'Bus', 'Bike', 'Walk'].indexOf(this.travelMean) != -1;
  }

  setTravelMean(travelMean: string) {
    this.travelMean = travelMean;
    this.setProp('travelMean');
    if (this.canDrive()) {
      if (this.geom != 'route') {
        this.geom = 'route';
        this.setProp('geom');
      }
    } else {
      if (this.geom == 'route') {
        this.geom = (this.defaults ? this.defaults.geom : 'straight');
        this.setProp('geom');
      }
    }
    this.changeEvent();
  }

  setGeom(geomStyle: string) {
    this.geom = geomStyle;
    this.setProp('geom');
    this.changeEvent();
  }
}



